import {getPathMarker, newLineEnded} from '../languageMarker';
import {SnippetProviderContext} from '../orchestrator';
import {normalizeLanguageId} from '../prompt';
import {SnippetProviderType, SnippetSemantics, SnippetWithProviderInfo} from '../snippetInclusion/snippets';
import {SnippetProvider} from './snippetProvider';

export class PathSnippetProvider extends SnippetProvider {
    public async buildSnippets(context: SnippetProviderContext): Promise<SnippetWithProviderInfo[]> {
        const {currentFile} = context;
        currentFile.languageId = normalizeLanguageId(currentFile.languageId);
        return [
            {
                provider: SnippetProviderType.Path,
                semantics: SnippetSemantics.Snippet,
                snippet: newLineEnded(getPathMarker(currentFile)),
                relativePath: currentFile.relativePath,
                startLine: 0,
                endLine: 0,
                score: 0,
            },
        ];
    }
}
