import {getFunctionPositions, isSupportedLanguageId} from '../parse';
import {DocumentInfo, DocumentInfoWithOffset, SnippetSelectionOption} from '../prompt';
import {CursorContextInfo} from './cursorContext';
import {SnippetProviderType, SnippetSemantics, SnippetWithProviderInfo} from './snippets';

class FifoCache<T> {
    private keys: string[] = [];
    private cache: {[key: string]: T} = {};
    private size: number;
    constructor(size: number) {
        this.size = size;
    }
    put(key: string, value: T) {
        this.cache[key] = value;
        if (this.keys.length > this.size) {
            this.keys.push(key);
            const leavingKey = this.keys.shift() ?? '';
            delete this.cache[leavingKey];
        }
    }
    get(key: string): T | undefined {
        return this.cache[key];
    }
}

/**
 * A snippet of code together with a relevance score
 */
export interface ScoredSnippetMarker {
    score: number;
    startLine: number;
    endLine: number;
}

export interface ScoredSnippet extends ScoredSnippetMarker {
    snippet: string;
    relativePath?: string;
}

export enum SortOptions {
    Ascending = 'ascending',
    Descending = 'descending',
    None = 'none',
}

class Tokenizer {
    private readonly stopsForLanguage: Set<string>;
    constructor(doc: DocumentInfo) {
        this.stopsForLanguage = SPECIFIC_STOPS.get(doc.languageId) ?? GENERIC_STOPS;
    }
    tokenize(a: string): Set<string> {
        return new Set<string>(splitIntoWords(a).filter(x => !this.stopsForLanguage.has(x)));
    }
}

/**
 * For a number of documents (the neighboring tabs),
 * associate to each document and its kind of window computation (as key)
 * the sequence b_1, ..., b_n, where
 * b_i is the set of tokens in the ith window --
 * e.g. for window length 10,
 * WINDOWED_TOKEN_SET_CACHE(doc)[0]
 * holds the tokens in the first 10 lines of the document.
 */
const WINDOWED_TOKEN_SET_CACHE = new FifoCache<Set<string>[]>(20);

/**
 * A matcher factory should be able to produce one matcher
 * for each document to which matches are to be found.
 * I.e. MatcherFactory.to(doc) is a matcher that matches against doc.
 */
export interface MatcherFactory {
    to(doc: DocumentInfoWithOffset): {
        findBestMatch(objectDoc: DocumentInfo): Promise<ScoredSnippet | undefined>;
        findMatches(
            objectDoc: DocumentInfo,
            snippetSelectionOption: SnippetSelectionOption | undefined,
            snippetSelectionK?: number
        ): Promise<ScoredSnippet[]>;
    };
}

/**
 * For a given document, extracts the best matching snippets from other documents
 * by comparing all of a set of windows in the object doc.
 */
export abstract class WindowedMatcher {
    protected referenceDoc: DocumentInfoWithOffset;
    protected tokenizer: Tokenizer;
    private _referenceTokens: Set<string> | undefined;

    protected abstract id(): string;
    protected abstract similarityScore(a: Set<string>, b: Set<string>): number;
    /**
     * Given an array of lines, returns an array of pairs <startLine, endLine> of indices,
     * such that each pair is a window of lines to consider adding.
     * startLine is inclusive, endLine is exclusive.
     * @param lines Lines of a source text, in order
     */
    protected abstract getWindowsDelineations(lines: string[]): [number, number][];

    protected abstract trimDocument(doc: DocumentInfoWithOffset): string;

    /**
     * Subclasses should implement this method to return the desired context info for tokenization
     * from the reference document. Will only be called after constructor is finished.
     * The tokenizer used in WindowedMatcher is a simple tokenizer for Jaccard similarity, NOT an
     * OpenAI model tokenizer.
     */
    protected abstract _getCursorContextInfo(referenceDoc: DocumentInfoWithOffset): CursorContextInfo;

    protected constructor(referenceDoc: DocumentInfoWithOffset, cursorContextFix: boolean) {
        this.referenceDoc = referenceDoc;
        this.tokenizer = new Tokenizer(referenceDoc); // Just uses language info from referenceDoc

        if (!cursorContextFix) {
            // If cursorContextFix is false, default to setting the reference tokens
            // in the constructor
            this._referenceTokens = this.tokenizer.tokenize(this.trimDocument(referenceDoc));
        }
    }

    /**
     * Lazy getter for referenceTokens since it relies on properties
     * that are not initialized in the constructor of WindowedMatcher
     * but in the constructor of its subclasses.
     */
    get referenceTokens(): Set<string> {
        // If cursorContextFix is active, _referenceTokens is not set in the constructor
        // (hence undefined) and _getCursorContextInfo is used instead.
        if (this._referenceTokens === undefined) {
            this._referenceTokens = this.tokenizer.tokenize(this._getCursorContextInfo(this.referenceDoc).context);
        }
        return this._referenceTokens;
    }

    /**
     * Returns a sorted array of snippets with their scores according to the sort option.
     * @param snippets ScoredSnippet[]
     *
     */
    sortScoredSnippets(snippets: ScoredSnippetMarker[], sortOption = SortOptions.Descending): ScoredSnippetMarker[] {
        return sortOption == SortOptions.Ascending
            ? snippets.sort((snippetA, snippetB) => (snippetA.score > snippetB.score ? 1 : -1))
            : sortOption == SortOptions.Descending
            ? snippets.sort((snippetA, snippetB) => (snippetA.score > snippetB.score ? -1 : 1))
            : snippets;
    }
    /**
     * Returns all snippet markers with their scores.
     * @param objectDoc
     *
     */
    retrieveAllSnippets(objectDoc: DocumentInfo, sortOption = SortOptions.Descending): ScoredSnippetMarker[] {
        const snippets: ScoredSnippetMarker[] = [];

        if (objectDoc.source.length === 0 || this.referenceTokens.size === 0) {
            return snippets;
        }

        const lines = objectDoc.source.split('\n');
        const key = this.id() + ':' + objectDoc.source;
        const tokensInWindows = WINDOWED_TOKEN_SET_CACHE.get(key) ?? [];
        // if the tokens are not cached, we need to compute them
        const needToComputeTokens = tokensInWindows.length == 0;
        const tokenizedLines = needToComputeTokens ? lines.map(this.tokenizer.tokenize, this.tokenizer) : [];

        // Compute the windows with the score
        for (const [index, [startLine, endLine]] of this.getWindowsDelineations(lines).entries()) {
            if (needToComputeTokens) {
                const tokensInWindow = new Set<string>();
                tokenizedLines.slice(startLine, endLine).forEach(x => x.forEach(tokensInWindow.add, tokensInWindow));
                tokensInWindows.push(tokensInWindow);
            }
            // Now tokensInWindows[index] contains the tokens in the window, whether we just computed them or not
            const tokensInWindow = tokensInWindows[index];
            const score = this.similarityScore(tokensInWindow, this.referenceTokens);
            snippets.push({
                score,
                startLine,
                endLine,
            });
        }

        // If we didn't get the token sets from the cache, time to put them there!
        if (needToComputeTokens) {
            WINDOWED_TOKEN_SET_CACHE.put(key, tokensInWindows);
        }

        return this.sortScoredSnippets(snippets, sortOption);
    }

    async findMatches(
        objectDoc: DocumentInfo,
        snippetSelectionOption = SnippetSelectionOption.BestMatch,
        snippetSelectionK?: number
    ): Promise<SnippetWithProviderInfo[]> {
        if (snippetSelectionOption == SnippetSelectionOption.BestMatch) {
            const snippet = await this.findBestMatch(objectDoc);
            return snippet ? [snippet] : [];
        }

        if (snippetSelectionOption == SnippetSelectionOption.TopK) {
            return (await this.findTopKMatches(objectDoc, snippetSelectionK)) || [];
        }

        return [];
    }

    /**
     * Returns the snippet from the object document
     * that is most similar to the reference Document
     * together with its Jaccard score
     *
     * @param objectDoc
     */
    async findBestMatch(objectDoc: DocumentInfo): Promise<SnippetWithProviderInfo | undefined> {
        if (objectDoc.source.length === 0 || this.referenceTokens.size === 0) {
            return undefined;
        }
        const lines = objectDoc.source.split('\n');
        const snippets = this.retrieveAllSnippets(objectDoc, SortOptions.Descending);

        // safe guard against empty lists
        if (snippets.length === 0) {
            return undefined;
        }
        if (snippets[0].score === 0) {
            return undefined;
        }

        // Compute the window with the best match
        const snippetCode = lines.slice(snippets[0].startLine, snippets[0].endLine).join('\n');
        return {
            snippet: snippetCode,
            semantics: SnippetSemantics.Snippet,
            provider: SnippetProviderType.NeighboringTabs,
            ...snippets[0],
        };
    }

    /**
     * Returns the snippet from the object document
     * that is most similar to the reference Document
     * together with its Jaccard score
     *
     * @param objectDoc
     */
    async findTopKMatches(
        objectDoc: DocumentInfo,
        snippetSelectionK = 1
    ): Promise<SnippetWithProviderInfo[] | undefined> {
        if (objectDoc.source.length === 0 || this.referenceTokens.size === 0 || snippetSelectionK < 1) {
            return undefined;
        }

        const lines = objectDoc.source.split('\n');

        const snippets = this.retrieveAllSnippets(objectDoc, SortOptions.Descending);
        if (snippets.length === 0 || snippets[0].score === 0) {
            return undefined;
        }

        const nonOverlappingSnippets: ScoredSnippetMarker[] = [snippets[0]];

        // step forward into snippets until finding a snippet that has no overlap with any snippet in nonOverlappingSnippets
        for (
            let currentIndex = 1;
            currentIndex < snippets.length && nonOverlappingSnippets.length < snippetSelectionK;
            currentIndex++
        ) {
            if (
                nonOverlappingSnippets.findIndex(
                    snippet =>
                        snippets[currentIndex].startLine < snippet.endLine &&
                        snippets[currentIndex].endLine > snippet.startLine
                ) == -1
            ) {
                nonOverlappingSnippets.push(snippets[currentIndex]);
            }
        }
        return nonOverlappingSnippets.map(snippetMarker => ({
            snippet: lines.slice(snippetMarker.startLine, snippetMarker.endLine).join('\n'),
            semantics: SnippetSemantics.Snippet,
            provider: SnippetProviderType.NeighboringTabs,
            ...snippetMarker,
        }));
    }
}

async function getNeighboringFunctions(neighbor: DocumentInfo) {
    let neighborFuncs: DocumentInfo[] = [];
    if (isSupportedLanguageId(neighbor.languageId)) {
        const funcPositions = await getFunctionPositions(neighbor.languageId, neighbor.source);
        for (let i = 0; i < funcPositions.length; i++) {
            let {startIndex, endIndex} = funcPositions[i];
            let func_source = neighbor.source.substring(startIndex, endIndex);
            neighborFuncs.push({
                source: func_source,
                relativePath: neighbor.relativePath,
                languageId: neighbor.languageId,
                uri: neighbor.uri,
            });
        }
    }
    return neighborFuncs;
}

export abstract class FunctionalMatcher extends WindowedMatcher {
    protected constructor(referenceDoc: DocumentInfoWithOffset, cursorContextFix: boolean) {
        super(referenceDoc, cursorContextFix);
    }

    getMatchingScore(neighborDoc: DocumentInfo): ScoredSnippet {
        const neighborDocTokens = this.tokenizer.tokenize(neighborDoc.source);
        const score = this.similarityScore(neighborDocTokens, this.referenceTokens);

        return {
            snippet: neighborDoc.source,
            score: score,
            startLine: 0,
            endLine: 0,
        };
    }

    override async findBestMatch(objectDoc: DocumentInfo): Promise<SnippetWithProviderInfo | undefined> {
        const snippets = await this.findMatches(objectDoc);
        // safe guard against empty lists
        if (snippets.length === 0) {
            return undefined;
        }
        if (snippets[0].score === 0) {
            return undefined;
        }
        return snippets[0];
    }

    override async findMatches(
        objectDoc: DocumentInfo,
        snippetSelectionOption?: SnippetSelectionOption,
        snippetSelectionK?: number
    ): Promise<SnippetWithProviderInfo[]> {
        if (objectDoc.source.length === 0 || this.referenceTokens.size === 0) {
            return [];
        }
        const neighborFuncs = await getNeighboringFunctions(objectDoc);

        if (neighborFuncs.length == 0) {
            // if no function was extracted, return default code snippets.
            const lines = objectDoc.source.split('\n');
            const snippets = this.retrieveAllSnippets(objectDoc, SortOptions.Descending);
            // safe guard against empty lists
            if (snippets.length === 0) {
                return [];
            }
            if (snippets[0].score === 0) {
                return [];
            }
            // Compute the window with the best match
            const snippetCode = lines.slice(snippets[0].startLine, snippets[0].endLine).join('\n');
            return [
                {
                    snippet: snippetCode,
                    semantics: SnippetSemantics.Snippet,
                    provider: SnippetProviderType.NeighboringTabs,
                    ...snippets[0],
                },
            ];
        }

        const snippets: SnippetWithProviderInfo[] = [];
        for (let func of neighborFuncs) {
            const snippet = this.getMatchingScore(func);
            snippets.push({
                semantics: SnippetSemantics.Function,
                provider: SnippetProviderType.NeighboringTabs,
                ...snippet,
            });
        }
        return snippets;
    }
}

/**
 * Split by non-alphanumeric characters
 */
export function splitIntoWords(a: string): string[] {
    return a.split(/[^a-zA-Z0-9]/).filter(x => x.length > 0);
}

