import {FileSystem} from './fileSystem';
import {newLineEnded} from './languageMarker';
import {extractLocalImportContext} from './localImportContext';
import {getSiblingFunctionStart} from './siblingFunctions';
import {CursorSnippetsPickingStrategy} from './snippetInclusion/cursorMatching';
import {NeighboringSnippetType, NeighboringTabsOption, getNeighborSnippets} from './snippetInclusion/neighboringFiles';
import {
    SingleSnippetProviderOptions,
    SnippetProviderOptions,
    SnippetProviderType,
    SnippetWithProviderInfo,
    processSnippetsForWishlist,
} from './snippetInclusion/snippets';
import {findEditDistanceScore} from './suffixMatchCriteria';
import {TokenizerName, getTokenizer} from './tokenization';
import {
    Priorities,
    PromptBackground,
    PromptChoices,
    PromptElementKind,
    PromptElementRanges,
    PromptWishlist,
} from './wishlist';
/** The 1-entry cache for the suffix */
let cachedSuffix: {text: string; tokens: number[]} = {text: '', tokens: []};

/**
 * Re. cached suffix, see the discussion in the PR: https://github.com/github/copilot-client/pull/2171
 * In an earlier version of the PR, we had declared variables cacheReusedCount,cacheReusablePromptTooLong
 * to track how often
 * (a) the cache is reused (cacheReusedCount) and
 * (b) when reusable, does the cache with the new prompt become too
 * long to be usable? (cacheReusablePromptTooLong)
 *
 * These are relevant to track in telemetry, and we will
 * implement the telemetry logs for these variables in a later iteration.
 * We have removed these variables in the PR since we were console.log'ging
 * it earlier, and that is not the preferred route.
 */

/** The maximum number of tokens in a prompt. */
export const MAX_PROMPT_LENGTH = 1500;

/** The maximum number of tokens that is used for calculate edit distance. */
export const MAX_EDIT_DISTANCE_LENGTH = 50;

/**
 * The number of tokens to reserve for prefix + suffix encoding.
 * This value comes from hponde.
 */
export const TOKENS_RESERVED_FOR_SUFFIX_ENCODING = 5;

/**
 * The maximal number of the final snippets to return.
 */
export const DEFAULT_NUM_OF_SNIPPETS: number = 4;

/**
 * Information about a document, not including the offset.
 */
export interface DocumentInfo {
    /** The file path of the document relative to its containing project or folder, if known. */
    relativePath?: string;
    /** The URI of the document. We can't pass URI class instances directly due to limitations of passing objects to the worker thread. */
    uri: string;
    /** The source text of the document. */
    source: string;
    /** The language identifier of the document. */
    languageId: string;
}

/**
 * Information about a document, including an offset corresponding to
 * the cursor position.
 */
export interface DocumentInfoWithOffset extends DocumentInfo {
    /** The offset in the document where we want the completion (0-indexed, between characters). */
    offset: number;
}

export enum LanguageMarkerOption {
    NoMarker = 'nomarker',
    Top = 'top', // Emulate that the first line of document is the language marker
    Always = 'always', // Always visible
}

export enum PathMarkerOption {
    NoMarker = 'nomarker',
    Top = 'top',
    Always = 'always',
}

export enum SnippetPositionOption {
    /** Include neighbors towards the beginning of the file */
    TopOfText = 'top',
    /** Include neighbors directly above the line where the cursor is */
    DirectlyAboveCursor = 'aboveCursor',
    /**
     * Include neighbors directly after the siblig insertion point
     * for languages that have one (above cursor otherwise)
     */
    AfterSiblings = 'afterSiblings',
}

export enum SnippetSelectionOption {
    /** Select the closest match.*/
    BestMatch = 'bestMatch',
    /** Select K closest match. */
    TopK = 'topK',
}

export enum LocalImportContextOption {
    /** Don't pull in any context for local imports. */
    NoContext = 'nocontext',
    /** Pull in declarations referenced by local imports. */
    Declarations = 'declarations',
}

export enum LineEndingOptions {
    ConvertToUnix = 'unix', // convert "\r\n" and "\r" line endings to the more token-efficient \n
    KeepOriginal = 'keep', // keep the original line endings (whether \r\n or \n)
}

export enum SuffixOption {
    /** Do not include a suffix in the prompt. */
    None = 'none',
    /** Allocate 15% of the available tokens to the suffix. */
    FifteenPercent = 'fifteenPercent',
}

export enum SuffixMatchOption {
    /** The method to use to compare suffix against cached suffix */
    /** The suffix and cached suffix are to be equal */
    Equal = 'equal',
    /** Use Edit distance */
    Levenshtein = 'levenshteineditdistance',
}

export enum SuffixStartMode {
    /** start from cursor by default */
    Cursor = 'cursor',
    /** start from cursor and trim start */
    CursorTrimStart = 'cursortrimstart',
    /** start from the first sibling function block */
    SiblingBlock = 'siblingblock',
    /** start from the first sibling function block and trim the leading whitespaces */
    SiblingBlockTrimStart = 'siblingblocktrimstart',
}

export class PromptOptions {
    /** The maximum prompt length in tokens */
    readonly maxPromptLength: number = MAX_PROMPT_LENGTH;
    /** Whether to include a language marker in the prompt */
    readonly languageMarker: LanguageMarkerOption = LanguageMarkerOption.Top;
    /** Whether to include a path marker in the prompt */
    readonly pathMarker: PathMarkerOption = PathMarkerOption.Top;
    /** Whether to pull in context for local imports */
    readonly localImportContext: LocalImportContextOption = LocalImportContextOption.Declarations;
    /** Where to include the neighboring tabs info */
    readonly snippetPosition: SnippetPositionOption = SnippetPositionOption.TopOfText;
    /** The number of snippets to include */
    readonly numberOfSnippets: number = DEFAULT_NUM_OF_SNIPPETS;
    /** Options for each snippet provider */
    readonly snippetProviderOptions: SnippetProviderOptions = {
        // This normalization ensures that neighboring tabs is always scored
        // above retrieved snippets:
        //   - neighboring tabs get positive scores, higher is better
        //   - retrieved snippets become negative scores, less negative is better
        'neighboring-tabs': {
            normalizationFunction: 'affine',
            normalizationParams: [1.0, 0.0], // identity
        },
        retrieval: {
            normalizationFunction: 'affine',
            normalizationParams: [-1.0, 0.0],
        },
        'symbol-def': {
            normalizationFunction: 'affine',
            normalizationParams: [1.0, 0.0],
            reservedSnippetCount: 2, // They are quite short, get two most outer caller functions
        },
        // Unused currently
        language: {
            normalizationFunction: 'affine',
            normalizationParams: [1.0, 0.0],
        },
        path: {
            normalizationFunction: 'affine',
            normalizationParams: [1.0, 0.0],
        },
    };
    /** Whether to include content from neighboring tabs in the prompt */
    readonly neighboringTabs: NeighboringTabsOption = NeighboringTabsOption.Eager;
    /** The type of snippets that neighboring tabs should extract */
    readonly neighboringSnippetTypes: NeighboringSnippetType = NeighboringSnippetType.NeighboringSnippets;
    /** Minimum length for matching snippet in neighboring tabs (only used for IndentationMatcher) */
    readonly indentationMinLength?: number;
    /** Maximum length for matching snippet in neighboring tabs (only used for IndentationMatcher) */
    readonly indentationMaxLength?: number;
    /** Whether to normalize line endings in the prompt */
    readonly lineEnding: LineEndingOptions = LineEndingOptions.ConvertToUnix;
    /** The percent of `maxPromptLength` to reserve for the suffix */
    readonly suffixPercent: number = 0;
    /** The percent of `maxPromptLength` to reserve for snippets */
    readonly snippetPercent: number = 0;
    /** The start mode to get suffix */
    readonly suffixStartMode: SuffixStartMode = SuffixStartMode.Cursor;
    /** TokenizerName used for prompt generation */
    readonly tokenizerName: TokenizerName = TokenizerName.cushman001;
    /** The threshold (in percent) for declaring match of new suffix with existing suffix */
    readonly suffixMatchThreshold: number = 0;
    /** The criteria to use for matching suffix with cachedSuffix, this is the percentage number*/
    readonly suffixMatchCriteria: SuffixMatchOption = SuffixMatchOption.Levenshtein;
    /** Selection options    */
    readonly snippetSelection?: SnippetSelectionOption;
    /** The number of snippets to select */
    readonly snippetSelectionK?: number;
    /** The threshold (as int) of length of the suffix only beyond which we will construct FIM requests */
    readonly fimSuffixLengthThreshold: number = 0;
    /** Whether the fix for getCursorContext is active. */
    readonly cursorContextFix: boolean = false;
    /** How to pick snippets for CursorHistoryMatcher */
    readonly cursorSnippetsPickingStrategy: CursorSnippetsPickingStrategy = CursorSnippetsPickingStrategy.CursorJaccard;

    constructor(readonly fs: FileSystem, options?: PartialPromptOptions) {
        if (options) {
            const selectionValue = options?.snippetSelection;
            if (selectionValue && !Object.values(SnippetSelectionOption).includes(selectionValue)) {
                throw new Error(`Invalid value for snippetSelection: ${selectionValue}`);
            }
            for (const key in options) {
                if (key !== 'snippetProviderOptions') {
                    // Note: this should really be improved so that the type
                    // system can statically prove that this won't blow up in
                    // run time (it will only do so if somewhat abused, right
                    // now, to be fair). For now, we have a @ts-ignore.
                    // @ts-ignore
                    this[key] = options[key];
                } else {
                    // key === 'snippetProviderOptions'
                    // For each provider, merge the options
                    const newOptions = options.snippetProviderOptions || {};
                    let provider: SnippetProviderType;
                    for (provider in newOptions) {
                        const providerOptions = newOptions[provider];
                        if (providerOptions) {
                            this.snippetProviderOptions[provider] = {
                                ...this.snippetProviderOptions[provider],
                                ...providerOptions,
                            };
                        }
                    }
                }
            }
        }

        if (this.suffixPercent < 0 || this.suffixPercent > 100) {
            throw new Error(`suffixPercent must be between 0 and 100, but was ${this.suffixPercent}`);
        }

        if (this.snippetPercent < 0 || this.snippetPercent > 100) {
            throw new Error(`snippetPercent must be between 0 and 100, but was ${this.snippetPercent}`);
        }

        if (this.suffixMatchThreshold < 0 || this.suffixMatchThreshold > 100) {
            throw new Error(`suffixMatchThreshold must be at between 0 and 100, but was ${this.suffixMatchThreshold}`);
        }

        // the threshold being -1 indicates we are not using this thresholding for the suffixes.
        if (this.fimSuffixLengthThreshold < -1) {
            throw new Error(`fimSuffixLengthThreshold must be at least -1, but was ${this.fimSuffixLengthThreshold}`);
        }

        if (this.indentationMinLength !== undefined && this.indentationMaxLength !== undefined) {
            if (this.indentationMinLength > this.indentationMaxLength) {
                throw new Error(
                    `indentationMinLength must be less than or equal to indentationMaxLength, but was ${this.indentationMinLength} and ${this.indentationMaxLength}`
                );
            }
            if (this.indentationMinLength < 0) {
                throw new Error(
                    `indentationMinLength must be greater than or equal to zero but was ${this.indentationMinLength}`
                );
            }
        }

        if (this.snippetSelection === SnippetSelectionOption.TopK && this.snippetSelectionK === undefined) {
            throw new Error('snippetSelectionK must be defined.');
        }

        if (
            this.snippetSelection === SnippetSelectionOption.TopK &&
            this.snippetSelectionK &&
            this.snippetSelectionK <= 0
        ) {
            throw new Error(`snippetSelectionK must be greater than 0, but was ${this.snippetSelectionK}`);
        }
    }
}

/**
 * This type is used to represent options that may be set by the caller, and
 * which will be completed to a {@link PromptOptions} object by filling with the
 * default options.
 *
 * The invocation is complicated by snippet provider options which should be
 * allowed to only set some of the options on some of the providers.
 */
export type PartialPromptOptions = Partial<Omit<PromptOptions, 'snippetProviderOptions'>> & {
    snippetProviderOptions?: Partial<Record<SnippetProviderType, Partial<SingleSnippetProviderOptions>>>;
};

export interface PromptInfo {
    prefix: string; // The prefix text
    suffix: string; // The suffix text
    prefixLength: number; // The length of the prefix in tokens
    suffixLength: number; // The length of the suffix in tokens
    promptChoices: PromptChoices; // Which choices were made when constructing the prompt
    promptBackground: PromptBackground; // All the prompt elements information when constructing the prompt
    promptElementRanges: PromptElementRanges; // Character ranges of prompt elements in the prompt
}

/**
 * A map that normalises common aliases of languageIds.
 */
const languageNormalizationMap: {[language: string]: string} = {
    javascriptreact: 'javascript',
    jsx: 'javascript',
    typescriptreact: 'typescript',
    jade: 'pug',
    cshtml: 'razor',
};

/**
 * Return a normalized form of a language id, by lower casing and combining
 * certain languageId's that are not considered distinct by promptlib.
 */
export function normalizeLanguageId(languageId: string): string {
    languageId = languageId.toLowerCase();
    return languageNormalizationMap[languageId] ?? languageId;
}

/**
 * Constructs a prompt to pass to the OpenAI API.
 *
 * @remarks
 * The current implementation does the simplest thing possible: It estimates the number of
 * characters that will fit within the maximum length of a prompt in tokens, then returns a
 * substring of doc that starts at some reasonable index and ends at offset, such that
 * (end - start) <= estimated_length.
 *
 * This will change in the future.
 *
 * @param doc - A DocumentInfoWithOffset
 * @param options - A PromptOptions which controls the prompt crafting behaviour.
 * @param neighbors - A list of neighboring documents, e.g., documents open in other
 *                    editor tabs.
 * @param snippets - A list of snippets with scores and provider information
 *                   to (potentially) include in the prompt.
 * @param lineCursorHistory - A The first key is the file name, and the second key
 *                            is the line number, the value is the number of cursor
 *                            focused.
 *
 * @returns A dictionary containing the prompt to pass to the OpenAI API and the
 * resulting prompt length in tokens.
 */
export async function getPrompt(
    fileSystem: FileSystem,
    doc: DocumentInfoWithOffset,
    options: PartialPromptOptions = {},
    neighbors: DocumentInfo[] = [],
    snippets: SnippetWithProviderInfo[] = [],
    lineCursorHistory?: Map<string, Map<number, number>>
): Promise<PromptInfo> {
    const completeOptions = new PromptOptions(fileSystem, options);

    const tokenizer = getTokenizer(completeOptions.tokenizerName);

    let useCachedSuffix = false;

    const {source, offset} = doc;
    if (offset < 0 || offset > source.length) {
        throw new Error(`Offset ${offset} is out of range.`);
    }
    doc.languageId = normalizeLanguageId(doc.languageId);

    // Make explicit the priorities implied by the different options
    const priorities = new Priorities();
    const directContextPriority = priorities.justBelow(Priorities.TOP);
    const languageMarkerPriority =
        completeOptions.languageMarker === LanguageMarkerOption.Always
            ? priorities.justBelow(Priorities.TOP)
            : priorities.justBelow(directContextPriority);
    const pathMarkerPriority =
        completeOptions.pathMarker === PathMarkerOption.Always
            ? priorities.justBelow(Priorities.TOP)
            : priorities.justBelow(directContextPriority);
    const localImportContextPriority = priorities.justBelow(directContextPriority);
    // There are two priorities for snippets: one for snippets that are
    // within the token budget defined by `snippetPercent` and one that are
    // outside of the token budget. The former are given a higher priority
    // when assembling the prompt, but the lower priority ones are still
    // included if there is space.
    const lowSnippetPriority = priorities.justBelow(localImportContextPriority);
    const highSnippetPriority = priorities.justAbove(directContextPriority);

    // The wishlist keeps track of what we would like to include in the prompt
    const promptWishlist = new PromptWishlist(tokenizer, completeOptions.lineEnding);

    // Language Snippet
    let languageSnippet = snippets.find(s => s.provider === SnippetProviderType.Language);
    let languageMarkerLine: number | undefined;
    if (completeOptions.languageMarker !== LanguageMarkerOption.NoMarker) {
        if (languageSnippet) {
            languageMarkerLine = promptWishlist.append(
                languageSnippet.snippet,
                PromptElementKind.LanguageMarker,
                languageMarkerPriority
            );
        }
    }

    // Path Snippet
    let pathSnippet = snippets.find(s => s.provider === SnippetProviderType.Path);
    let pathMarkerLine: number | undefined;
    if (completeOptions.pathMarker !== PathMarkerOption.NoMarker && pathSnippet !== undefined) {
        // not just newline
        if (pathSnippet.snippet.length > 0) {
            pathMarkerLine = promptWishlist.append(
                pathSnippet.snippet,
                PromptElementKind.PathMarker,
                pathMarkerPriority
            );
        }
    }

    // imports get inserted after the language / path markers, but before anything from the file itself
    if (completeOptions.localImportContext !== LocalImportContextOption.NoContext) {
        for (const localImportContext of await extractLocalImportContext(doc, completeOptions.fs)) {
            promptWishlist.append(
                newLineEnded(localImportContext),
                PromptElementKind.ImportedFile,
                localImportContextPriority
            );
        }
    }

    // Get neighbor snippets and combine with externally provided snippets.
    const neighborSnippets =
        completeOptions.neighboringTabs === NeighboringTabsOption.None
            ? []
            : neighbors.length === 0
            ? []
            : await getNeighborSnippets(
                  doc,
                  neighbors,
                  completeOptions.neighboringSnippetTypes,
                  completeOptions.neighboringTabs,
                  completeOptions.cursorContextFix,
                  completeOptions.indentationMinLength,
                  completeOptions.indentationMaxLength,
                  completeOptions.snippetSelection,
                  completeOptions.snippetSelectionK,
                  lineCursorHistory,
                  completeOptions.cursorSnippetsPickingStrategy
              );

    // Remove language & path snippets from the list of snippets prior to wishlist fulfillment
    // Because they have already been included above
    snippets = snippets.filter(
        s => s.provider !== SnippetProviderType.Language && s.provider !== SnippetProviderType.Path
    );

    const allSnippets = [...snippets, ...neighborSnippets];

    /** Deferred function to add the prepared snippets to the wishlist at
     * the location at that points
     */
    function addSnippetsNow(): void {
        // assign priorities based on budget
        const budget = Math.round((completeOptions.snippetPercent / 100.0) * completeOptions.maxPromptLength);
        const processedSnippets = processSnippetsForWishlist(
            allSnippets,
            doc.languageId,
            tokenizer,
            completeOptions.snippetProviderOptions,
            {priorities, low: lowSnippetPriority, high: highSnippetPriority},
            completeOptions.numberOfSnippets,
            budget
        );
        // add to wishlist
        processedSnippets.forEach(snippet => {
            // TODO: this should be cleaned up https://github.com/github/copilot-client/issues/3303
            let kind = PromptElementKind.SimilarFile;
            if (snippet.provider === SnippetProviderType.Retrieval) {
                kind = PromptElementKind.RetrievalSnippet;
            } else if (snippet.provider == SnippetProviderType.SymbolDef) {
                kind = PromptElementKind.SymbolDefinition;
            }
            promptWishlist.append(
                snippet.announcedSnippet,
                kind,
                snippet.priority,
                snippet.tokens,
                snippet.normalizedScore
            );
        });
    }

    if (completeOptions.snippetPosition === SnippetPositionOption.TopOfText) {
        addSnippetsNow();
    }

    /** The ids of lines of BeforeCursor elements in the promptlib */
    const source_lines: number[] = [];
    let directContext: string | undefined;
    directContext = source.substring(0, offset);

    if (completeOptions.snippetPosition === SnippetPositionOption.DirectlyAboveCursor) {
        // first add all lines from directContext except the current partial one (if there is one)
        const lastLineStart = directContext.lastIndexOf('\n') + 1; // 0 if no newline
        const directContextBeforePartialLastLine = directContext.substring(0, lastLineStart);
        const partialLastLine = directContext.substring(lastLineStart); // may be "" if none
        promptWishlist
            .appendLineForLine(
                directContextBeforePartialLastLine,
                PromptElementKind.BeforeCursor,
                directContextPriority
            )
            .forEach(id => source_lines.push(id));
        addSnippetsNow();
        // then add the partial last line, if it's not empty
        if (partialLastLine.length > 0) {
            source_lines.push(
                promptWishlist.append(partialLastLine, PromptElementKind.AfterCursor, directContextPriority)
            );
            // note that the second-to-last line requires the last line, or it makes no sense
            if (source_lines.length > 1) {
                promptWishlist.require(source_lines[source_lines.length - 2], source_lines[source_lines.length - 1]);
            }
        }
    } else {
        promptWishlist
            .appendLineForLine(directContext, PromptElementKind.BeforeCursor, directContextPriority)
            .forEach(id => source_lines.push(id));
    }

    // the marker options `Top` intend to simulate the file beginning with the markers
    // so the markers should depend on the first line of source code --
    // if that is missing, so should they be
    if (
        LanguageMarkerOption.Top === completeOptions.languageMarker &&
        source_lines.length > 0 &&
        languageMarkerLine !== undefined
    ) {
        promptWishlist.require(languageMarkerLine, source_lines[0]);
    }
    if (
        PathMarkerOption.Top === completeOptions.pathMarker &&
        source_lines.length > 0 &&
        pathMarkerLine !== undefined
    ) {
        languageMarkerLine
            ? promptWishlist.require(pathMarkerLine, languageMarkerLine)
            : promptWishlist.require(pathMarkerLine, source_lines[0]);
    }

    // Language markers are not necessary if the pathmarker already indicates extension
    if (languageMarkerLine !== undefined && pathMarkerLine !== undefined) {
        promptWishlist.exclude(pathMarkerLine, languageMarkerLine);
    }

    let actualSuffix = source.slice(offset);

    if (completeOptions.suffixPercent === 0 || actualSuffix.length <= completeOptions.fimSuffixLengthThreshold) {
        return promptWishlist.fulfill(completeOptions.maxPromptLength);
    } else {
        let offset = doc.offset;
        if (
            completeOptions.suffixStartMode !== SuffixStartMode.Cursor &&
            completeOptions.suffixStartMode !== SuffixStartMode.CursorTrimStart
        ) {
            offset = await getSiblingFunctionStart(doc);
        }

        // First, construct prefix using the wishlist.
        const availableTokens = completeOptions.maxPromptLength - TOKENS_RESERVED_FOR_SUFFIX_ENCODING;
        let prefixTargetTokens = Math.floor((availableTokens * (100 - completeOptions.suffixPercent)) / 100);
        let promptInfo = promptWishlist.fulfill(prefixTargetTokens);

        // Then construct suffix using the remaining tokens.  The prefix/wishlist computation usually
        // does not consume all the available tokens, so we reallocate any remaining to the suffix.
        const suffixTargetTokens = availableTokens - promptInfo.prefixLength;
        let suffixText = source.slice(offset);
        if (
            completeOptions.suffixStartMode === SuffixStartMode.SiblingBlockTrimStart ||
            completeOptions.suffixStartMode === SuffixStartMode.CursorTrimStart
        ) {
            suffixText = suffixText.trimStart();
        }

        const suffix = tokenizer.takeFirstTokens(suffixText, suffixTargetTokens);

        // If there is not enough text to fill the suffix, allocate the extra tokens to
        // the prefix and recompute it.
        if (suffix.tokens.length <= suffixTargetTokens - 3) {
            // only recompute prefix if we can increase its length by >= 3 tokens
            prefixTargetTokens = availableTokens - suffix.tokens.length;
            promptInfo = promptWishlist.fulfill(prefixTargetTokens);
        }

        if (completeOptions.suffixMatchCriteria === SuffixMatchOption.Equal) {
            if (
                suffix.tokens.length === cachedSuffix.tokens.length &&
                suffix.tokens.every((v, i) => v === cachedSuffix.tokens[i])
            ) {
                useCachedSuffix = true;
            }
        } else if (completeOptions.suffixMatchCriteria === SuffixMatchOption.Levenshtein) {
            // if the current suffix is an empty string, then do not add the cachedSuffix
            if (suffix.tokens.length > 0 && completeOptions.suffixMatchThreshold > 0) {
                // only compare the first MAX_EDIT_DISTANCE_LENGTH tokens to speed up.
                const dist = findEditDistanceScore(
                    suffix.tokens.slice(0, MAX_EDIT_DISTANCE_LENGTH),
                    cachedSuffix.tokens.slice(0, MAX_EDIT_DISTANCE_LENGTH)
                )?.score;
                if (
                    100 * dist <
                    completeOptions.suffixMatchThreshold * Math.min(MAX_EDIT_DISTANCE_LENGTH, suffix.tokens.length)
                ) {
                    useCachedSuffix = true;
                }
            }
        }

        if (useCachedSuffix === true && cachedSuffix.tokens.length <= suffixTargetTokens) {
            // Similar to codeblock above for actual suffix, if there is not enough text to fill the suffix, allocate extra
            // tokens to the prefix and recompute it.
            if (cachedSuffix.tokens.length <= suffixTargetTokens - 3) {
                // only recompute prefix if we can increase its length by >= 3 tokens
                prefixTargetTokens = availableTokens - cachedSuffix.tokens.length;
                promptInfo = promptWishlist.fulfill(prefixTargetTokens);
            }
            promptInfo.suffix = cachedSuffix.text;
            promptInfo.suffixLength = cachedSuffix.tokens.length;
        } else {
            // EITHER suffix is not close to cachedSuffix OR cachedSuffix is too long. In either case, we devolve to actual suffix.
            // Note: when cachedSuffix is too long, we have the option of truncating the cachedSuffix using takeFirstTokens but that
            // would already invalidate the cache property that we want (in SPM mode).
            promptInfo.suffix = suffix.text;
            promptInfo.suffixLength = suffix.tokens.length;

            // log current count, reset the suffix and counts
            // TODO:
            // About 0.1% of the time log the cachedReusedCount and the suffix used
            // to telemetry. Discussion ongoing on Slack.
            // Right now, console.logging cacheReusedCount so as to make the project.
            cachedSuffix = suffix;
        }

        return promptInfo;
    }
}
