import {DocumentInfoWithOffset} from '../prompt';
import {CursorContextInfo, getCursorContext} from './cursorContext';
import {FunctionalMatcher, WindowedMatcher} from './selectRelevance';
import {getBasicWindowDelineations, getIndentationWindowsDelineations} from './windowDelineations';

export class FixedWindowSizeJaccardMatcher extends WindowedMatcher {
    private windowLength: number;
    private cursorContextFix: boolean;

    private constructor(referenceDoc: DocumentInfoWithOffset, windowLength: number, cursorContextFix: boolean) {
        super(referenceDoc, cursorContextFix);
        this.windowLength = windowLength;
        this.cursorContextFix = cursorContextFix;
    }

    static FACTORY = (windowLength: number, cursorContextFix: boolean) => {
        return {
            to: (referenceDoc: DocumentInfoWithOffset) =>
                new FixedWindowSizeJaccardMatcher(referenceDoc, windowLength, cursorContextFix),
        };
    };

    protected id(): string {
        return 'fixed:' + this.windowLength;
    }

    protected getWindowsDelineations(lines: string[]): [number, number][] {
        return getBasicWindowDelineations(this.windowLength, lines);
    }

    protected trimDocument(doc: DocumentInfoWithOffset): string {
        return doc.source.slice(0, doc.offset).split('\n').slice(-this.windowLength).join('\n');
    }

    protected _getCursorContextInfo(referenceDoc: DocumentInfoWithOffset): CursorContextInfo {
        return getCursorContext(referenceDoc, {
            maxLineCount: this.windowLength,
            cursorContextFix: this.cursorContextFix,
        });
    }

    protected similarityScore(a: Set<string>, b: Set<string>): number {
        return computeScore(a, b);
    }
}

/**
 * A matcher that tries to find a maximal coherent snippet
 * of length between min and max number of lines
 * in the object document.
 * A snippet is coherent if:
 * * it is a contiguous sequence of lines
 * * for each two nodes a, b, there are respective ancestors that are both included and siblings to each other
 */
export class IndentationBasedJaccardMatcher extends WindowedMatcher {
    private indentationMinLength: number;
    private indentationMaxLength: number;
    private languageId: string;
    private cursorContextFix: boolean;

    private constructor(
        referenceDoc: DocumentInfoWithOffset,
        indentationMinLength: number,
        indentationMaxLength: number,
        cursorContextFix: boolean
    ) {
        super(referenceDoc, cursorContextFix);
        this.indentationMinLength = indentationMinLength;
        this.indentationMaxLength = indentationMaxLength;
        this.languageId = referenceDoc.languageId;
        this.cursorContextFix = cursorContextFix;
    }

    static FACTORY = (indentationMinLength: number, indentationMaxLength: number, cursorContextFix: boolean) => {
        return {
            to: (referenceDoc: DocumentInfoWithOffset) =>
                new IndentationBasedJaccardMatcher(
                    referenceDoc,
                    indentationMinLength,
                    indentationMaxLength,
                    cursorContextFix
                ),
        };
    };

    protected id(): string {
        return `indent:${this.indentationMinLength}:${this.indentationMaxLength}:${this.languageId}`;
    }

    protected getWindowsDelineations(lines: string[]): [number, number][] {
        const windows: [number, number][] = getIndentationWindowsDelineations(
            lines,
            this.languageId,
            this.indentationMinLength,
            this.indentationMaxLength
        );
        if (windows.length > 0) {
            return windows;
        } else if (lines.length < this.indentationMinLength) {
            // Return the entire file if the indentation matcher returns nothing and the file is short enough
            return [[0, lines.length]];
        } else {
            return [];
        }
    }

    protected trimDocument(doc: DocumentInfoWithOffset): string {
        return doc.source.slice(0, doc.offset).split('\n').slice(-this.indentationMaxLength).join('\n');
    }

    protected _getCursorContextInfo(referenceDoc: DocumentInfoWithOffset): CursorContextInfo {
        return getCursorContext(referenceDoc, {
            maxLineCount: this.indentationMaxLength,
            cursorContextFix: this.cursorContextFix,
        });
    }

    protected similarityScore(a: Set<string>, b: Set<string>): number {
        return computeScore(a, b);
    }
}

export class FunctionJaccardMatcher extends FunctionalMatcher {
    private indentationMinLength: number | undefined;
    private indentationMaxLength: number | undefined;
    private languageId: string;
    private cursorContextFix: boolean;

    protected id(): string {
        return 'function:' + this.windowLength;
    }
    protected getWindowsDelineations(lines: string[]): [number, number][] {
        if (this.indentationMaxLength !== undefined && this.indentationMinLength !== undefined) {
            return getIndentationWindowsDelineations(
                lines,
                this.languageId,
                this.indentationMinLength,
                this.indentationMaxLength
            );
        }
        return getBasicWindowDelineations(this.windowLength, lines);
    }
    private windowLength: number;

    private constructor(
        referenceDoc: DocumentInfoWithOffset,
        windowLength: number,
        cursorContextFix: boolean,
        indentationMinLength: number | undefined,
        indentationMaxLength: number | undefined
    ) {
        super(referenceDoc, cursorContextFix);
        this.windowLength = windowLength;
        this.indentationMinLength = indentationMinLength;
        this.indentationMaxLength = indentationMaxLength;
        this.languageId = referenceDoc.languageId;
        this.cursorContextFix = cursorContextFix;
    }

    static FACTORY = (
        windowLength: number,
        cursorContextFix: boolean,
        indentationMinLength?: number,
        indentationMaxLength?: number
    ) => {
        return {
            to: (referenceDoc: DocumentInfoWithOffset) =>
                new FunctionJaccardMatcher(
                    referenceDoc,
                    windowLength,
                    cursorContextFix,
                    indentationMinLength,
                    indentationMaxLength
                ),
        };
    };

    protected trimDocument(doc: DocumentInfoWithOffset): string {
        return doc.source.slice(0, doc.offset).split('\n').slice(-this.windowLength).join('\n');
    }

    protected _getCursorContextInfo(referenceDoc: DocumentInfoWithOffset): CursorContextInfo {
        return getCursorContext(referenceDoc, {
            maxLineCount: this.windowLength,
            cursorContextFix: this.cursorContextFix,
        });
    }

    protected similarityScore(a: Set<string>, b: Set<string>): number {
        return computeScore(a, b);
    }
}

/**
 * Compute the Jaccard metric of number of elements in the intersection
 * divided by number of elements in the union
 */
export function computeScore(a: Set<string>, b: Set<string>) {
    const intersection = new Set();
    a.forEach(x => {
        if (b.has(x)) {
            intersection.add(x);
        }
    });
    return intersection.size / (a.size + b.size - intersection.size);
}
