import {promises as fsp} from 'fs';

/**
 * The `FileStat`-type represents metadata about a file
 */
export interface FileStat {
    /**
     * The creation timestamp in milliseconds elapsed since January 1, 1970 00:00:00 UTC.
     */
    ctime: number;

    /**
     * The modification timestamp in milliseconds elapsed since January 1, 1970 00:00:00 UTC.
     *
     * *Note:* If the file changed, it is important to provide an updated `mtime` that advanced
     * from the previous value. Otherwise there may be optimizations in place that will not show
     * the updated file contents in an editor for example.
     */

    mtime: number;
    /**
     * The size in bytes.
     *
     * *Note:* If the file changed, it is important to provide an updated `size`. Otherwise there
     * may be optimizations in place that will not show the updated file contents in an editor for
     * example.
     */
    size: number;
}

/**
 * A basic file-system interface for reading files and checking their mtime.
 */
export abstract class FileSystem {
    /**
     * Read the entire contents of the given file.
     *
     * cf https://nodejs.org/api/fs.html#fs_fspromises_readfile_path_options
     */
    abstract readFile(uri: string): Promise<Uint8Array>;

    /**
     * Get the mtime in milliseconds of the given file.
     *
     * cf https://nodejs.org/api/fs.html#fs_stats_mtimems
     */
    abstract mtime(uri: string): Promise<number>;

    abstract stat(uri: string): Promise<FileStat>;
}

export const defaultFileSystem: FileSystem = {
    readFile(uri: string) {
        return fsp.readFile(uri);
    },

    async mtime(uri: string) {
        let stat = await fsp.stat(uri);
        return stat.mtimeMs;
    },

    async stat(uri: string): Promise<FileStat> {
        const stat = await fsp.stat(uri);
        return {
            ctime: stat.ctimeMs,
            mtime: stat.mtimeMs,
            size: stat.size,
        };
    },
};
