import {commentBlockAsSingles} from '../languageMarker';
import {Tokenizer} from '../tokenization';
import {Priorities} from '../wishlist';
import {ScoredSnippet} from './selectRelevance';

/** Indicates what provider produced a given snippet. */
export enum SnippetProviderType {
    NeighboringTabs = 'neighboring-tabs',
    Retrieval = 'retrieval',
    SymbolDef = 'symbol-def',
    Language = 'language',
    Path = 'path',
}

/**
 * The semantics of a snippet. For example, some providers
 * might always produce a snippet that is a complete function
 * whereas others might produce a snippet that are inherhently
 * partial.
 */
export enum SnippetSemantics {
    /** The contents of the snippet is a function. */
    Function = 'function',
    /** The contents of the snippet is an unspecified snippet. */
    Snippet = 'snippet',
    /** The following are from hover text */
    Variable = 'variable',
    Parameter = 'parameter',
    Method = 'method',
    Class = 'class',
    Module = 'module',
    Alias = 'alias',
    Enum = 'enum member',
    Interface = 'interface',
}

/** Extends a ScoredSnippet with information about its provider. */
export interface SnippetWithProviderInfo extends ScoredSnippet {
    /** The provider that created this snippet. */
    provider: SnippetProviderType;
    /** The semantical meaning of the snippet's contents. */
    semantics: SnippetSemantics;
}

export interface SingleSnippetProviderOptions {
    /** Which function to apply for score normalization */
    normalizationFunction: 'affine';
    /** The parameters/coefficients for the score normalization function */
    normalizationParams: number[];
    /**
     * Number of snippets reserved to this provider, disregarding whether other
     * providers have higher-scoring snippets.
     */
    reservedSnippetCount?: number;
}

/**
 * The options for all snippet providers.
 */
export type SnippetProviderOptions = Record<SnippetProviderType, SingleSnippetProviderOptions>;

/**
 * A snippet with a normalized score
 */
export interface SnippetWithNormalizedScore extends Omit<SnippetWithProviderInfo, 'score'> {
    providerScore: number;
    normalizedScore: number;
}

/**
 * The most processed form of a snippet that contains fully rendered and
 * prioritized snippets, including their token count. Can be used directly
 * with the wishlist. The priority is a wishlist priority, not a score.
 */
export interface ProcessedSnippet {
    /** The announced (i.e. formatted) snippet */
    announcedSnippet: string;
    /** Wishlish priority */
    priority: number;
    /** The score of the snippet as given directly by the provider */
    providerScore: number;
    /** The normalized score */
    normalizedScore: number;
    /** The number of tokens in the (announced) snippet */
    tokens: number;
    /** The provider */
    provider: SnippetProviderType;
    /** Relative path */
    relativePath?: string;
}

/**
 * A map from semantics enum to a human / LLM-readable label that we
 * include when announcing a snippet.
 */
const snippetSemanticsToString: {[key in SnippetSemantics]: string} = {
    [SnippetSemantics.Function]: 'function',
    [SnippetSemantics.Snippet]: 'snippet',
    [SnippetSemantics.Variable]: 'variable',
    [SnippetSemantics.Parameter]: 'parameter',
    [SnippetSemantics.Method]: 'method',
    [SnippetSemantics.Class]: 'class',
    [SnippetSemantics.Module]: 'module',
    [SnippetSemantics.Alias]: 'alias',
    [SnippetSemantics.Enum]: 'enum member',
    [SnippetSemantics.Interface]: 'interface',
};

/**
 * Formats a snippet for inclusion in the prompt.
 *
 * This does three things:
 * 1. Adds an announcement headline.
 * 2. Formats each line as a single-line comment in the language of the target doc.
 * 3. Adds a trailing newline if one was not already there.
 */
export function announceSnippet(snippet: Omit<SnippetWithProviderInfo, 'score'>, targetDocLanguageId: string): string {
    const semantics = snippetSemanticsToString[snippet.semantics];
    const headline = snippet.relativePath
        ? `Compare this ${semantics} from ${snippet.relativePath}:`
        : `Compare this ${semantics}:`;
    let headlinedSnippet = headline + '\n' + snippet.snippet;
    if (!headlinedSnippet.endsWith('\n')) {
        headlinedSnippet += '\n';
    }
    return commentBlockAsSingles(headlinedSnippet, targetDocLanguageId);
}

export function normalizeSnippetScore<S extends {provider: SnippetProviderType; score: number}>(
    snippet: S,
    providerOptions: SnippetProviderOptions
): Omit<S, 'score'> & {providerScore: number; normalizedScore: number} {
    const options = providerOptions[snippet.provider];
    if (!options) {
        throw new Error('Unknown snippet provider: ' + snippet.provider);
    }
    const {score: providerScore, ...snippetRem} = snippet;
    let normalizedScore = providerScore;
    if (options.normalizationFunction === 'affine') {
        const [a, b] = options.normalizationParams;
        normalizedScore = a * providerScore + b;
    } else {
        throw new Error(
            `Unknown normalization function ${options.normalizationFunction} for snippet provider ${snippet.provider}`
        );
    }
    return {
        ...snippetRem, // Remove score
        providerScore,
        normalizedScore,
    };
}

/**
 * Sorts snippets in-place in descending order by their normalized score.
 */
function sortSnippetsDescending<S extends {normalizedScore: number}>(snippets: S[]): void {
    snippets.sort((a, b) => b.normalizedScore - a.normalizedScore);
}

/**
 * Selects `numberOfSnippets` simultanously adhering to two principles:
 *   1. Select the best `reservedSnippetCount` snippets from each provider,
 *      according to their normalized score.
 *   2. Select the best snippets according to normalized score across all
 *      providers, until `numberOfSnippets` have been selected in total.
 *
 * NOTE: If the total number of `reservedSnippetCount` exceeds
 * `numberOfSnippets`, then this method throws an exception.
 *
 * The returned snippets are returned in order of decreasing normalized score,
 * i.e. the best snippets are first in each list.
 */
export function selectSnippets(
    snippets: SnippetWithProviderInfo[],
    numberOfSnippets: number,
    providerOptions: SnippetProviderOptions
): {reserved: SnippetWithNormalizedScore[]; candidates: SnippetWithNormalizedScore[]} {
    if (numberOfSnippets == 0) {
        return {reserved: [], candidates: []};
    }
    // Compute all the normalized scores
    const normalizedSnippets: SnippetWithNormalizedScore[] = snippets.map(snippet =>
        normalizeSnippetScore(snippet, providerOptions)
    );
    // Split snippets by provider
    const snippetsByProvider = new Map<SnippetProviderType, SnippetWithNormalizedScore[]>();
    let provider: SnippetProviderType;
    for (provider in providerOptions) {
        snippetsByProvider.set(provider, []);
    }
    for (const snippet of normalizedSnippets) {
        let snippets = snippetsByProvider.get(snippet.provider);
        if (!snippets) {
            throw new Error('Unknown snippet provider: ' + snippet.provider);
        }
        snippets.push(snippet);
    }
    for (const [_provider, snippets] of snippetsByProvider) {
        sortSnippetsDescending(snippets);
    }
    // Pick all reserved snippets
    let reserved: SnippetWithNormalizedScore[] = [];
    for (provider in providerOptions) {
        const options = providerOptions[provider];
        const count = options.reservedSnippetCount || 0;
        if (count > 0) {
            const snippets = snippetsByProvider.get(provider) || [];
            reserved = reserved.concat(snippets.slice(0, count));
            snippetsByProvider.set(provider, snippets.slice(count));
        }
    }
    sortSnippetsDescending(reserved);
    let candidates: SnippetWithNormalizedScore[] = [];
    if (reserved.length > numberOfSnippets) {
        throw new Error('Reserved snippet count exceeds number of snippets');
    }
    if (reserved.length < numberOfSnippets) {
        // Flatten and resort all remaining snippets
        const remaining = Array.from(snippetsByProvider.values()).flat();
        sortSnippetsDescending(remaining);
        // Pick the best remaining snippets
        candidates = remaining.slice(0, numberOfSnippets - reserved.length);
    }
    return {reserved, candidates};
}

/**
 * "Processes" a list of snippets and assigns priorities.
 *
 * The snippets will get their scores normalized, will be announced, and will be
 * selected according to best-normalized score while adhering to any
 * reservations stipulated in `providerOptions`. See {@link selectSnippets} for
 * details.
 *
 * The returned snippets will then be assigned priorites descendingly from
 * `priorities.high` respectively `priorities.low`, or not included at all,
 * according to two parameters `totalPrioritized` and `highPriorityBudget`.
 *
 * The snippets are also returned by increasing normalized score. If they are
 * enqueued in this order in the wishlist, then the highest-scoring snippets
 * will appear closest to the cursor.
 *
 * By definition, a reserved snippet may be selected with higher priority than a
 * non-reserved candidate snippet with better normalized score. The reserved
 * snippet is then guaranteed to be selected in the wishlist before the
 * non-reserved snippet, but if both are ultimately selected, the non-reserved
 * one will appear last in the prompt.
 *
 * @param snippets The snippets to process.
 * @param providerOptions The options for each snippet provider.
 * @param priorities Priorities to assign to snippets: high priority is assigned
 * to the first `highPriorityBudget` tokens, low priority to the remaining
 * `totalPrioritized` snippets.
 * @param totalPrioritized The maximal number of snippets to return, i.e. if
 * more snippets are passed in, we remove the least desirable.
 * @param highPriorityBudget The number of tokens to use for high priority
 * snippets
 */
export function processSnippetsForWishlist(
    snippets: SnippetWithProviderInfo[],
    targetDocLanguageId: string,
    tokenizer: Tokenizer,
    providerOptions: SnippetProviderOptions,
    priorities: {priorities: Priorities; low: number; high: number},
    totalPrioritized: number,
    highPriorityBudget: number
): ProcessedSnippet[] {
    // selectSnippets returns in order of decreasing normalized score, i.e. best
    // first in each list.
    const {reserved, candidates} = selectSnippets(snippets, totalPrioritized, providerOptions);
    let usedBudget = 0;
    let processedSnippets: ProcessedSnippet[] = [];
    let nextHighPriority = priorities.high;
    let nextLowPriority = priorities.low;
    function process(snippet: SnippetWithNormalizedScore, usedBudget: number): number {
        const announced = announceSnippet(snippet, targetDocLanguageId);
        const tokens = tokenizer.tokenLength(announced);
        let priority: number;
        if (usedBudget + tokens <= highPriorityBudget) {
            priority = nextHighPriority;
            nextHighPriority = priorities.priorities.justBelow(priority);
        } else {
            priority = nextLowPriority;
            nextLowPriority = priorities.priorities.justBelow(priority);
        }
        processedSnippets.push({
            announcedSnippet: announced,
            provider: snippet.provider,
            providerScore: snippet.providerScore,
            normalizedScore: snippet.normalizedScore,
            priority,
            tokens,
            relativePath: snippet.relativePath,
        });
        return usedBudget + tokens;
    }
    // We first assign priorities to the reserved snippets.
    // Then to the remaining candidates.
    for (const snippet of [...reserved, ...candidates]) {
        if (processedSnippets.length >= totalPrioritized) {
            break;
        }
        usedBudget = process(snippet, usedBudget);
    }
    // Re-sort the list by increasing normalized score
    sortSnippetsDescending(processedSnippets);
    processedSnippets.reverse();
    return processedSnippets;
}
