const promptlib = require('./lib');
const {parentPort} = require('worker_threads');
const {defaultFileSystem} = require('./fileSystem');

const workerFileSystem = {
    readFile: path => {
        return new Promise((resolve, reject) => {
            parentPort.once('readFileRes', data => {
                if (data) {
                    resolve(data);
                } else {
                    reject();
                }
            });
            parentPort.emit('readFileReq', path);
        });
    },
    mtime: path => {
        return new Promise((resolve, reject) => {
            parentPort.once('mtimeRes', data => {
                if (data) {
                    resolve(data);
                } else {
                    reject();
                }
            });
            parentPort.emit('mtimeReq', path);
        });
    },
};

parentPort.on('message', async ({id, fn, args}) => {
    try {
        if (fn === 'getPrompt') {
            //TODO: Figure out why `workerFileSystem` is deadlocking in TS files with imports
            let res = await promptlib.getPrompt(defaultFileSystem, ...args);
            parentPort.postMessage({id, res});
        } else {
            let res = await promptlib[fn](...args);
            parentPort.postMessage({id, res});
        }
    } catch (err) {
        parentPort.postMessage({id, err});
    }
});
