/**
 * @fileoverview
 * This is the main class for using Elidable Texts,
 * a way to represent a larger, possibly multimodal document
 * to be used as prompt to an LLM.
 *
 * That document will need to be shortened to fit into a token budget.
 * Elidable Texts shorten it by dropping the least relevant lines,
 * and replacing them by ellipses (`[...]`).
 *
 * A typical way of using this class is to
 * - create an ElidableText from a some inputs,
 * - then call `makePrompt` to get a prompt that fits into a token budget.
 *
 * Like this:
 * ```
 * const question = new ElidableText(
 *   [`Could you look over Albert's PR and check whether we need to add tests for that one file?`, 0.5],
 *   [`They made the following changes to file ${this.filename} (in git diff format):`, 0.9],
 *   [this.diff, 0.7], // this.diff is an already constructed ElidableText
 *   [`The file now looks like this:`, 0.95],
 *   [documentWithLanguage, 0.8],
 *   [`Should I write tests for that?`, 1],
 * );
 * const prompt = question.makePrompt(1000); // makes sure no more than 1000 tokens
 * ```
 */

import {DocumentInfo} from '../prompt';
import {getTokenizer} from '../tokenization';
import {elidableTextForSourceCode} from './fromSourceCode';
import {LineWithValueAndCost} from './lineWithValueAndCost';

type InterpretableAsElidableText = string | ElidableText | DocumentInfo;

export class ElidableText {
    lines: LineWithValueAndCost[] = [];

    /**
     * Create a text from a list of chunks, which can be strings or ElidableTexts.
     * Supplying a number to the chunk corresponds to a priority.
     * If the chunk is already elidable, the priorities are multiplied.
     *
     * If x is an ElidableText, then ElidableText(x) is the same as x.
     * @param chunks
     */
    constructor(...chunks: (InterpretableAsElidableText | [InterpretableAsElidableText, number])[]) {
        const lines: LineWithValueAndCost[] = [];
        for (const chunk of chunks) {
            // if array, take the second element as priority
            const value = Array.isArray(chunk) ? chunk[1] : 1;
            const input = Array.isArray(chunk) ? chunk[0] : chunk;
            if (typeof input === 'string') {
                input.split('\n').forEach(line => lines.push(new LineWithValueAndCost(line, value)));
            } else if (input instanceof ElidableText) {
                lines.push(...input.lines.map(line => line.copy().adjustValue(value)));
            } else if ('source' in input && 'languageId' in input) {
                lines.push(...elidableTextForSourceCode(input).lines.map(line => line.copy().adjustValue(value)));
            }
        }
        this.lines = lines;
    }

    adjust(multiplier: number): void {
        this.lines.forEach(line => line.adjustValue(multiplier));
    }

    /** Change the cost of lines according to a specified function; e.g. to take into account different tokenziers */
    recost(coster = (x: string) => getTokenizer().tokenLength(x + '\n')): void {
        this.lines.forEach(line => line.recost(coster));
    }

    /**
     * Elides lines to make the prompt fit into a token budget.
     * This is done by dropping the least desirable lines.
     * @param maxTokens The maximum number of tokens to allow.
     * @param ellipsis The string to use for ellipses.
     * @param indentEllipses If true, indents ellipses with the minimum indentation of the elided lines.
     * - only guarantees ellipses' indentation if there are sufficient tokens for it.
     * - does not currently work well for tab-indented lines (will insert spaces instead of tabs)
     * @param strategy "removeLeastDesirable" will greedily remove undesirable lines,
     * "removeLeastBangForBuck" will remove the line that has the lowest value/cost ratio.
     * The former is more likely to elide continguous blocks and thus often feels more natural.
     * The latter can be more frugal by being less tempted to elide things like single whitespace lines.
     * @param tokenizer The tokenizer to use for tokenizing the prompt.
     */
    makePrompt(
        maxTokens: number,
        ellipsis = '[...]',
        indentEllipses = true,
        strategy: 'removeLeastDesirable' | 'removeLeastBangForBuck' = 'removeLeastDesirable',
        tokenizer = getTokenizer()
    ): string {
        // the function is factored out so that we can be sure that this.lines is not mutated
        const lines = this.lines.map(line => line.copy());
        return makePrompt(lines, maxTokens, ellipsis, indentEllipses, strategy, tokenizer);
    }
}

/**
 * Worker function for {@link ElidableText.makePrompt}.
 * All params are the same except
 * @param lines The lines desired to be in the prompt -- will be mutated.
 */
function makePrompt(
    lines: LineWithValueAndCost[],
    maxTokens: number,
    ellipsis: string,
    indentEllipses: boolean,
    strategy: 'removeLeastDesirable' | 'removeLeastBangForBuck',
    tokenizer: ReturnType<typeof getTokenizer>
) {
    if (tokenizer.tokenLength(ellipsis + '\n') > maxTokens) {
        throw new Error('maxTokens must be larger than the ellipsis length');
    }
    if (strategy === 'removeLeastBangForBuck') {
        // adjust all line's values by dividing by their cost
        lines.forEach(line => line.adjustValue(1 / line.cost));
    }
    // infiniteWorth is 1 bigger than the most desirable line
    const infiniteWorth = lines.reduce((a, b) => Math.max(a, b.value), 0) + 1;
    // indentationInfinity is longer than the longest line.text
    const infiniteIndentation = lines.reduce((a, b) => Math.max(a, b.text.length), 0) + 1;
    // the test of equality to possibly indented ellipses is whether it's identical to the trimmed ellipsis
    const trimmedEllipsis = ellipsis.trim();

    let totalCost = lines.reduce((sum, line) => sum + line.cost, 0);
    let defensiveCounter = lines.length + 1;
    while (totalCost > maxTokens && defensiveCounter-- >= -1) {
        // find the least desirable line
        const leastDesirable = lines.reduce((least, line) => {
            if (line.value < least.value) {
                return line;
            } else {
                return least;
            }
        });
        // drop it, but replace with ellipsis if it's between two non-ellipsis lines
        const index = lines.indexOf(leastDesirable);
        // the right indentation is the indentation of the line (if not blank) or the most recent non-blank line
        const mostRecentNonBlankLine = lines
            .slice(0, index + 1)
            .reverse()
            .find(line => line.text.trim() !== '') ?? {text: ''};
        const indentation = indentEllipses
            ? Math.min(
                  // the smallest one of: the index indentation, and the before and after indentation _should they be ellipses_
                  // note that whitespace lines do not count
                  mostRecentNonBlankLine.text.match(/^\s*/)?.[0].length ?? 0,
                  lines[index - 1]?.text.trim() === trimmedEllipsis
                      ? lines[index - 1]?.text.match(/^\s*/)?.[0].length ?? 0
                      : infiniteIndentation,
                  lines[index + 1]?.text.trim() === trimmedEllipsis
                      ? lines[index + 1]?.text.match(/^\s*/)?.[0].length ?? 0
                      : infiniteIndentation
              )
            : 0;

        // Known limitation: indentation will be off for tab-indented lines
        const insert = ' '.repeat(indentation) + ellipsis;
        const newEllipis = new LineWithValueAndCost(
            insert,
            infiniteWorth,
            tokenizer.tokenLength(insert + '\n'),
            // validate only loosely -- infiniteWorth may be > 1, and that's ok here
            'loose'
        );

        // now replace this line by the new ellipsis
        lines.splice(index, 1, newEllipis);
        // then delete the lines before and after if they are ellipses
        if (lines[index + 1]?.text.trim() === trimmedEllipsis) {
            lines.splice(index + 1, 1);
        }
        if (lines[index - 1]?.text.trim() === trimmedEllipsis) {
            lines.splice(index - 1, 1);
        }

        const newTotalCost = lines.reduce((sum, line) => sum + line.cost, 0);
        // if we don't make progress _and_ it's all ellipses, we've reached the case where we need to forgo indentation
        if (newTotalCost >= totalCost && lines.every(line => line.value === infiniteWorth)) {
            indentEllipses = false;
        }
        totalCost = newTotalCost;
    }
    if (defensiveCounter < 0) {
        // this should not have happened, throw an error
        throw new Error(
            `Infinite loop in ElidableText.makePrompt: Defensive counter < 0 in ElidableText.makePrompt with end text:\n ${lines
                .map(line => line.text)
                .join('\n')}`
        );
    }
    return lines.map(line => line.text).join('\n');
}
