import {dirname, extname, join} from 'path';
import {SyntaxNode} from 'web-tree-sitter';
import {FileSystem} from './fileSystem';
import {getFirstPrecedingComment, parseTreeSitter, queryExports} from './parse';
import {DocumentInfoWithOffset} from './prompt';

/**
 * Resolve the import `imp`, which is a TypeScript `import` statement appearing in a module whose
 * path is `importerPath`, using `fs`.
 *
 * If the import is not a local import (i.e., the import path does not start with `.`), or the file
 * it imports has an extension other than `.ts`, return `null`.
 */
function resolveLocalTypeScriptImport(importerPath: string, imp: SyntaxNode): string | null {
    let src = imp.namedChild(1)?.text.slice(1, -1);
    if (!src || !src.startsWith('.')) return null;

    if (extname(src) === '') {
        src = src + '.ts';
    } else if (extname(src) !== '.ts') {
        return null;
    }

    return join(dirname(importerPath), src);
}

/**
 * Get a list of all names imported by `imp` (possibly with renaming), where
 * `imp` must be a TypeScript `import` statement.
 */
function getTypescriptImportedNames(imp: SyntaxNode): {name: string; alias: string | undefined}[] {
    let names = [];
    if (imp.namedChild(0)?.type === 'import_clause') {
        let importClause = imp.namedChild(0);
        if (importClause?.namedChild(0)?.type === 'named_imports') {
            let namedImports = importClause.namedChild(0);
            for (let namedImport of namedImports?.namedChildren ?? []) {
                if (namedImport.type === 'import_specifier') {
                    const name = namedImport.childForFieldName('name')?.text;
                    if (name) {
                        const alias = namedImport.childForFieldName('alias')?.text;
                        names.push({name, alias});
                    }
                }
            }
        }
    }
    return names;
}

/**
 * Module exports are modelled as a mapping from names to lists of declarations.
 *
 * The same name can map to multiple declarations for function signature exports.
 */
type Exports = Map<string, string[]>;

const exportsCache = new Map<string, {mtime: number; exports: Exports}>();

const EXPORTS_CACHE_LOW_WATER_MARK = 1000;
const EXPORTS_CACHE_HIGH_WATER_MARK = 2000;

/**
 * Given a definition of a function or type, return its name and a corresponding
 * TypeScript declaration that only contains types but no code.
 *
 * For ambient declarations, we recursively process the wrapped declaration.
 *
 * For interfaces, enums, and type aliases, they are returned as-is.
 *
 * For function signatures and declarations, we turn them into signatures as
 * explained in the documentation for `extractTypeScriptFunctionDeclaration` below.
 *
 * Classes are turned into ambient declarations, using `extractTypeScriptMemberDeclaration`
 * to recursively process their members.
 */
function extractTypeScriptDeclaration(srcString: string, defn: SyntaxNode | null): {name: string; decl: string} {
    let name = defn?.childForFieldName('name')?.text ?? '';

    switch (defn?.type) {
        case 'ambient_declaration':
            return extractTypeScriptDeclaration(srcString, defn.namedChild(0));
        case 'interface_declaration':
        case 'enum_declaration':
        case 'type_alias_declaration':
            return {name, decl: defn.text};
        case 'function_declaration':
        case 'function_signature':
            return {name, decl: extractTypeScriptFunctionDeclaration(srcString, defn)};
        case 'class_declaration': {
            let memberDecls = extractTypeScriptBodyDecls(srcString, defn);
            let decl = '';
            if (memberDecls) {
                let body = defn.childForFieldName('body')!;
                // first, we take everything up to and including the start of the body
                decl = `declare ${srcString.substring(defn.startIndex, body.startIndex + 1)}`;

                // then we add the member declarations
                decl += memberDecls.map(d => '\n' + d).join('');

                // finally, we add the closing brace
                decl += `\n}`;
            }
            return {name, decl};
        }
    }

    return {name, decl: ''};
}

/**
 * Given a definition or a signature of a TypeScript function or method, return
 * a corresponding TypeScript declaration that only contains types but no code.
 *
 * We do this by taking the prefix up to and including the return type
 * annotation if present, or the parameter list otherwise. This does not always
 * yield a valid signature, for example in the presence of default parameters
 * and parameter properties, but it's good enough for the purposes of prompt
 * crafting.
 *
 * For functions (but nor for methods) we additionally prepend the `declare` keyword
 * to turn it into an ambient declaration.
 */
function extractTypeScriptFunctionDeclaration(srcString: string, defn: SyntaxNode): string {
    const endIndex = defn.childForFieldName('return_type')?.endIndex ?? defn.childForFieldName('parameters')?.endIndex;
    if (endIndex !== undefined) {
        let signature = srcString.substring(defn.startIndex, endIndex) + ';';
        if (defn.type === 'function_declaration' || defn.type === 'function_signature') {
            return 'declare ' + signature;
        } else {
            return signature;
        }
    }
    return '';
}

/**
 * Get the indentation of `node`, that is, the shortest string of spaces and
 * tabs that starts at the beginning of a line and ends at the beginning of
 * `node`. If no such string exists, return `undefined`.
 */
function getIndentation(srcString: string, node: SyntaxNode): string | undefined {
    // skip backwards over tabs and spaces
    let i = node.startIndex - 1;
    while (i >= 0 && (srcString[i] === ' ' || srcString[i] === '\t')) {
        i--;
    }
    // if we're at the beginning of the file or at a newline, we've found our indentation
    if (i < 0 || srcString[i] === '\n') {
        return srcString.substring(i + 1, node.startIndex);
    }
    // otherwise something more complicated is going on, so we'll return undefined
    return undefined;
}

/**
 * Get the doc comment attached to `node`.
 *
 * This is defined as all text between the start of the first comment in the
 * longest unbroken run of comments preceding `node` and the start of `node`,
 * or the empty string if no such run exists.
 *
 * In particular, the "doc comment" will include leading whitespace of the
 * node, but not of the first comment in the run.
 */
export function getDocComment(srcString: string, node: SyntaxNode): string {
    const docCommentNode = getFirstPrecedingComment(node);
    return docCommentNode ? srcString.substring(docCommentNode.startIndex, node.startIndex) : '';
}

/**
 * Given a definition of a (non-private) class member, return a corresponding
 * TypeScript declaration that only contains types but no code.
 *
 * For ambient members, we recursively process the wrapped declaration.
 *
 * For method definitions and signatures we use `extractTypeScriptFunctionDeclaration` above.
 *
 * For fields, we chop off the initializer if present.
 */
function extractTypeScriptMemberDeclaration(srcString: string, defn: SyntaxNode): string {
    // filter out private members
    if (defn?.firstChild?.type === 'accessibility_modifier' && defn.firstChild.text === 'private') {
        return '';
    }

    const commentNode = getFirstPrecedingComment(defn);
    const indentation = getIndentation(srcString, commentNode ?? defn) ?? '  ';
    const docComment = getDocComment(srcString, defn);

    switch (defn.type) {
        case 'ambient_declaration':
            const inner = defn.namedChild(0);
            if (!inner) {
                return '';
            }
            return indentation + docComment + extractTypeScriptMemberDeclaration(srcString, inner);
        case 'method_definition':
        case 'method_signature':
            return indentation + docComment + extractTypeScriptFunctionDeclaration(srcString, defn);
        case 'public_field_definition': {
            // chop off initialiser if any
            let endIndex = defn.childForFieldName('type')?.endIndex ?? defn.childForFieldName('name')?.endIndex;
            if (endIndex !== undefined) {
                return indentation + docComment + srcString.substring(defn.startIndex, endIndex) + ';';
            }
        }
    }
    return '';
}

/**
 * Given a definition of a class or interface, return a list of TypeScript declarations of
 * its members.
 *
 * @param srcString the source code of the file containing the class or interface
 * @param defn the class or interface definition
 */
function extractTypeScriptBodyDecls(srcString: string, defn: SyntaxNode): string[] | undefined {
    let body = defn.childForFieldName('body');
    if (!body) {
        return;
    }

    let memberDecls = body.namedChildren.map(member => extractTypeScriptMemberDeclaration(srcString, member));
    // filter out members for which we could not extract a declaration
    return memberDecls.filter(decl => decl);
}

/**
 * Construct a map representing declarations exported by the file at the given `uri`, which is
 * written in language `lang`.
 *
 * For TypeScript, this map associates exported types and functions with the (string representation of)
 * their declarations/signatures.
 *
 * For other languages, declaration extraction is not currently supported, so we always return an
 * empty map.
 */
async function getExportedDeclarations(uri: string, lang: string, fs: FileSystem): Promise<Exports> {
    let exports = new Map<string, string[]>();

    let mtime = -1;
    try {
        mtime = await fs.mtime(uri);
    } catch {
        // if anything wet wrong getting the mtime, simply report no exports
        return exports;
    }

    let entry = exportsCache.get(uri);
    if (entry && entry.mtime === mtime) return entry.exports;

    if (lang === 'typescript') {
        let tree = null;
        try {
            let srcBytes = await fs.readFile(uri);
            let srcString = srcBytes.toString();
            tree = await parseTreeSitter(lang, srcString);

            for (let em of queryExports(lang, tree.rootNode)) {
                for (let ec of em.captures) {
                    let exp = ec.node;
                    if (exp.type === 'export_statement') {
                        let decl = exp.childForFieldName('declaration');

                        // skip declarations with syntax errors
                        if (decl?.hasError()) {
                            continue;
                        }

                        let {name, decl: exportedDecl} = extractTypeScriptDeclaration(srcString, decl);

                        if (name) {
                            exportedDecl = getDocComment(srcString, exp) + exportedDecl;
                            let exportedDecls = exports.get(name);
                            if (!exportedDecls) {
                                exportedDecls = [];
                                exports.set(name, exportedDecls);
                            }
                            exportedDecls.push(exportedDecl);
                        }
                    }
                }
            }
        } catch {
            // if anything went wrong during reading or parsing the file, simply report no exports
        } finally {
            if (tree) tree.delete();
        }
    }

    // if cache has more than EXPORTS_CACHE_HIGH_WATER_MARK entries, remove the oldest entries
    // to bring its size down to EXPORTS_CACHE_LOW_WATER_MARK
    if (exportsCache.size > EXPORTS_CACHE_HIGH_WATER_MARK) {
        for (let key of exportsCache.keys()) {
            exportsCache.delete(key);
            if (exports.size <= EXPORTS_CACHE_LOW_WATER_MARK) {
                break;
            }
        }
    }

    exportsCache.set(uri, {mtime, exports});
    return exports;
}

/**
 * Find all import statements in the given subtree.
 */
function getTypeScriptImports(root: SyntaxNode) {
    let imports = [];
    // note that import statements can only appear at the toplevel, so we can simply iterate over
    // the root's children
    for (let toplevelStmt of root.namedChildren) {
        if (toplevelStmt.type === 'import_statement') {
            imports.push(toplevelStmt);
        }
    }
    return imports;
}

/** Regex to match a TypeScript import statement. */
const localImportRegex = /^\s*import\s*(type|)\s*\{[^}]*\}\s*from\s*['"]\./gm;

/**
 * Return the offset of the newline character after the last local import
 * statement in the given string.
 * Returns -1 if the source has no import statements.
 */
function lastTypeScriptLocalImportOffset(source: string): number {
    let lastImport = -1;
    localImportRegex.lastIndex = -1; // restart the regex from the beginning
    let m: RegExpExecArray | null;
    do {
        m = localImportRegex.exec(source);
        if (m) {
            lastImport = localImportRegex.lastIndex + m.length;
        }
    } while (m);

    if (lastImport === -1) {
        return -1;
    }

    const newlineAfterLastImport = source.indexOf('\n', lastImport);
    return newlineAfterLastImport !== -1 ? newlineAfterLastImport : source.length;
}

/**
 * Extract context information from local TypeScript imports.
 */
async function extractTypeScriptLocalImportContext(source: string, uri: string, fs: FileSystem): Promise<string[]> {
    let languageId = 'typescript';
    let localImportContext: string[] = [];

    // Only parse up to and including the last line containing an import statement
    const lastImportOffset = lastTypeScriptLocalImportOffset(source);
    if (lastImportOffset === -1) {
        return localImportContext;
    }
    source = source.substring(0, lastImportOffset);

    let tree = await parseTreeSitter(languageId, source);
    try {
        for (let imp of getTypeScriptImports(tree.rootNode)) {
            let srcUri = resolveLocalTypeScriptImport(uri, imp);
            if (!srcUri) continue;

            let importedNames = getTypescriptImportedNames(imp);
            if (importedNames.length === 0) continue;

            let exports = await getExportedDeclarations(srcUri, languageId, fs);
            for (let importedName of importedNames)
                if (exports.has(importedName.name)) {
                    localImportContext.push(...exports.get(importedName.name)!);
                    // if the import came through an alias, we could simulate this by adding an alias
                    // declaration, but the model seems to figure this out by itself, so we don't
                }
        }
    } finally {
        tree.delete();
    }
    return localImportContext;
}

/**
 * Extract context information from local imports.
 */
export async function extractLocalImportContext(doc: DocumentInfoWithOffset, fs?: FileSystem): Promise<string[]> {
    let {source, uri, languageId} = doc;
    if (fs) if (languageId === 'typescript') return extractTypeScriptLocalImportContext(source, uri, fs);
    return [];
}
