import {DocumentInfo, DocumentInfoWithOffset, SnippetSelectionOption} from '../prompt';
import {CursorContextInfo, getCursorContext} from './cursorContext';
import {computeScore} from './jaccardMatching';
import {ScoredSnippetMarker, SortOptions, WindowedMatcher} from './selectRelevance';
import {SnippetProviderType, SnippetSemantics, SnippetWithProviderInfo} from './snippets';
import {getBasicWindowDelineations} from './windowDelineations';

/**
 * 3 different strategies to pick up snippets by cursor history.
 * CursorOnly:
 *      Pick up the most clicked snippets.
 * CursorJaccard:
 *      Pick up the most clicked snippets, and re-sort them by Jaccard similarity.
 * JaccardCursor:
 *      Pick up the most similar snippet by Jaccard similarity, and re-sort them by the number of clicked.
 */
export enum CursorSnippetsPickingStrategy {
    CursorOnly = 'cursoronly',
    CursorJaccard = 'cursorjaccard',
    JaccardCursor = 'jaccardcursor',
}

class FifoCache<T> {
    private keys: string[] = [];
    private cache: {[key: string]: T} = {};
    private size: number;
    constructor(size: number) {
        this.size = size;
    }
    put(key: string, value: T) {
        this.cache[key] = value;
        if (this.keys.length > this.size) {
            this.keys.push(key);
            const leavingKey = this.keys.shift() ?? '';
            delete this.cache[leavingKey];
        }
    }
    get(key: string): T | undefined {
        return this.cache[key];
    }
}

/**
 * For a number of documents (the neighboring tabs),
 * associate to each document and its kind of window computation (as key)
 * the sequence b_1, ..., b_n, where
 * b_i is the set of tokens in the ith window --
 * e.g. for window length 10,
 * WINDOWED_TOKEN_SET_CACHE(doc)[0]
 * holds the tokens in the first 10 lines of the document.
 */
const WINDOWED_TOKEN_SET_CACHE = new FifoCache<Set<string>[]>(20);

class CustomizedFixedWindowSizeJaccardMatcher extends WindowedMatcher {
    private windowLength: number;
    private cursorContextFix: boolean;

    public constructor(referenceDoc: DocumentInfoWithOffset, windowLength: number, cursorContextFix: boolean) {
        super(referenceDoc, cursorContextFix);
        this.windowLength = windowLength;
        this.cursorContextFix = cursorContextFix;
    }

    protected id(): string {
        return 'CustomizedFixedWindowSizeJaccardMatcher:' + this.windowLength;
    }

    protected getWindowsDelineations(lines: string[]): [number, number][] {
        return getBasicWindowDelineations(this.windowLength, lines);
    }

    protected trimDocument(doc: DocumentInfoWithOffset): string {
        return doc.source.slice(0, doc.offset).split('\n').slice(-this.windowLength).join('\n');
    }

    protected _getCursorContextInfo(referenceDoc: DocumentInfoWithOffset): CursorContextInfo {
        return getCursorContext(referenceDoc, {
            maxLineCount: this.windowLength,
            cursorContextFix: this.cursorContextFix,
        });
    }

    protected similarityScore(a: Set<string>, b: Set<string>): number {
        return computeScore(a, b);
    }

    /**
     * Returns all snippet markers with their scores.
     * @param objectDoc
     * @param candidates This is a Map from start line to end line. This funciton only returns the snippets that the start line and end line are in the map.
     *                   We need it because we want to ignore the code snippets that are not clicked. See the references in CursorHistoryMatcher.
     *
     */
    override retrieveAllSnippets(
        objectDoc: DocumentInfo,
        sortOption = SortOptions.Descending,
        candidates?: Map<number, number>
    ): ScoredSnippetMarker[] {
        const snippets: ScoredSnippetMarker[] = [];

        if (objectDoc.source.length === 0 || this.referenceTokens.size === 0) {
            return snippets;
        }

        const lines = objectDoc.source.split('\n');
        const key = this.id() + ':' + objectDoc.source;
        const tokensInWindows = WINDOWED_TOKEN_SET_CACHE.get(key) ?? [];
        // if the tokens are not cached, we need to compute them
        const needToComputeTokens = tokensInWindows.length == 0;
        const tokenizedLines = needToComputeTokens ? lines.map(this.tokenizer.tokenize, this.tokenizer) : [];

        // Compute the windows with the score
        for (const [index, [startLine, endLine]] of this.getWindowsDelineations(lines).entries()) {
            if (needToComputeTokens) {
                const tokensInWindow = new Set<string>();
                tokenizedLines.slice(startLine, endLine).forEach(x => x.forEach(tokensInWindow.add, tokensInWindow));
                tokensInWindows.push(tokensInWindow);
            }

            if (candidates !== undefined && candidates.get(startLine) !== endLine) {
                continue;
            }

            // Now tokensInWindows[index] contains the tokens in the window, whether we just computed them or not
            const tokensInWindow = tokensInWindows[index];
            const score = this.similarityScore(tokensInWindow, this.referenceTokens);
            snippets.push({
                score,
                startLine,
                endLine,
            });
        }

        // If we didn't get the token sets from the cache, time to put them there!
        if (needToComputeTokens) {
            WINDOWED_TOKEN_SET_CACHE.put(key, tokensInWindows);
        }

        return this.sortScoredSnippets(snippets, sortOption);
    }
}

/**
 * Find the best matches from a neighbor file by cursor history information.
 */
export class CursorHistoryMatcher {
    private windowLength: number;
    private lineCursorHistory: Map<string, Map<number, number>>;
    private jaccardMatcher: CustomizedFixedWindowSizeJaccardMatcher;
    private strategy: CursorSnippetsPickingStrategy;

    private constructor(
        referenceDoc: DocumentInfoWithOffset,
        windowLength: number,
        lineCursorHistory: Map<string, Map<number, number>>,
        strategy: CursorSnippetsPickingStrategy,
        cursorContextFix: boolean
    ) {
        this.windowLength = windowLength;
        this.lineCursorHistory = lineCursorHistory;
        this.jaccardMatcher = new CustomizedFixedWindowSizeJaccardMatcher(referenceDoc, windowLength, cursorContextFix);
        this.strategy = strategy;
    }

    static FACTORY = (
        windowLength: number,
        lineCursorHistory: Map<string, Map<number, number>>,
        strategy: CursorSnippetsPickingStrategy,
        cursorContextFix: boolean
    ) => {
        return {
            to: (referenceDoc: DocumentInfoWithOffset) =>
                new CursorHistoryMatcher(referenceDoc, windowLength, lineCursorHistory, strategy, cursorContextFix),
        };
    };

    /**
     * Returns a sorted array of snippets with their scores according to the sort option.
     * @param snippets ScoredSnippetMarker[]
     *
     */
    private sortScoredSnippets(
        snippets: ScoredSnippetMarker[],
        sortOption = SortOptions.Descending
    ): ScoredSnippetMarker[] {
        return sortOption == SortOptions.Ascending
            ? snippets.sort((snippetA, snippetB) => (snippetA.score > snippetB.score ? 1 : -1))
            : sortOption == SortOptions.Descending
            ? snippets.sort((snippetA, snippetB) => (snippetA.score > snippetB.score ? -1 : 1))
            : snippets;
    }

    private markerToSnippet(nonOverlappingSnippets: ScoredSnippetMarker[], lines: string[]): SnippetWithProviderInfo[] {
        return nonOverlappingSnippets.map(snippetMarker => ({
            snippet: lines.slice(snippetMarker.startLine, snippetMarker.endLine).join('\n'),
            provider: SnippetProviderType.NeighboringTabs,
            semantics: SnippetSemantics.Snippet,
            ...snippetMarker,
        }));
    }

    public async findMatches(
        objectDoc: DocumentInfo,
        snippetSelectionOption = SnippetSelectionOption.BestMatch,
        snippetSelectionK?: number
    ): Promise<SnippetWithProviderInfo[]> {
        if (snippetSelectionOption == SnippetSelectionOption.BestMatch) {
            const bestMatch = await this.findBestMatch(objectDoc);
            if (bestMatch === undefined) {
                return [];
            } else {
                return [bestMatch];
            }
        }

        if (snippetSelectionOption == SnippetSelectionOption.TopK) {
            return (await this.findTopKMatches(objectDoc, snippetSelectionK)) || [];
        }

        return [];
    }

    /**
     * Returns the snippet from the object document that is most similar to the reference Document.
     * - The returned score
     *   - For CursorOnly, the returned score is the number clicks.
     *   - For CursorJaccard and JaccardCursor, The returned score is the sum of the number of clicks and the Jaccard similarity.
     *     The number of clicks is always a integer, and the Jaccard similarity is always a float less than 1.
     *     So we can assume that the code snippet will be sorted by the number of clicks first, and then by the Jaccard similarity.
     * - Algorithm
     *   - CursorOnly
     *     Pick up the most clicked snippets. If there are multiply snippets with the same number of clicks,
     *     it will return the middle one in the file.
     *   - CursorJaccard
     *     Pick up the most clicked snippets, and re-sort them by Jaccard similarity.
     *   - JaccardCursor
     *     It is equivalent to FixedWindowSizeJaccardMatcher.findBestMatch, because we just pick the most similar one snippet.
     * @param objectDoc
     */
    public async findBestMatch(objectDoc: DocumentInfo): Promise<SnippetWithProviderInfo | undefined> {
        if (objectDoc.source.length === 0) {
            return undefined;
        }

        if (this.strategy === CursorSnippetsPickingStrategy.CursorOnly) {
            let snippetsByCursor = this.retrieveCursorSnippets(objectDoc);
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            if (snippetsByCursor.length === 0) {
                return undefined;
            }

            const bestCursorScore = Math.max(...snippetsByCursor.map(s => s.score));
            const bestSnippets = snippetsByCursor.filter(s => s.score === bestCursorScore);
            const bestInMiddle = bestSnippets.sort((a, b) => a.startLine - b.startLine)[
                Math.floor(bestSnippets.length / 2)
            ];

            const lines = objectDoc.source.split('\n');
            return {
                snippet: lines.slice(bestInMiddle.startLine, bestInMiddle.endLine).join('\n'),
                provider: SnippetProviderType.NeighboringTabs,
                semantics: SnippetSemantics.Snippet,
                ...bestInMiddle,
            };
        } else if (this.strategy === CursorSnippetsPickingStrategy.CursorJaccard) {
            let snippetsByCursor = this.retrieveCursorSnippets(objectDoc);
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            if (snippetsByCursor.length === 0) {
                return undefined;
            }

            const bestCursorScore = Math.max(...snippetsByCursor.map(s => s.score));
            const bestSnippetsByCursor = [];
            const bestSnippetsBoundaryByCursor = new Map<number, number>();
            for (const snippet of snippetsByCursor) {
                if (snippet.score === bestCursorScore) {
                    bestSnippetsByCursor.push(snippet);
                    bestSnippetsBoundaryByCursor.set(snippet.startLine, snippet.endLine);
                }
            }

            const bestSnippets = this.jaccardMatcher.retrieveAllSnippets(
                objectDoc,
                SortOptions.Descending,
                bestSnippetsBoundaryByCursor
            );

            if (bestSnippets.length === 0) {
                return undefined;
            }

            const bestSnippet = bestSnippets[0];
            for (const snippet of snippetsByCursor) {
                if (snippet.startLine === bestSnippet.startLine && snippet.endLine === bestSnippet.endLine) {
                    bestSnippet.score += snippet.score;
                    break;
                }
            }

            const lines = objectDoc.source.split('\n');
            return {
                snippet: lines.slice(bestSnippet.startLine, bestSnippet.endLine).join('\n'),
                provider: SnippetProviderType.NeighboringTabs,
                semantics: SnippetSemantics.Snippet,
                ...bestSnippet,
            };
        } else if (this.strategy === CursorSnippetsPickingStrategy.JaccardCursor) {
            const bestSnippet = await this.jaccardMatcher.findBestMatch(objectDoc);
            if (bestSnippet === undefined) {
                return undefined;
            }

            let snippetsByCursor = this.retrieveCursorSnippets(objectDoc);
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            if (snippetsByCursor.length === 0) {
                return undefined;
            }

            for (const snippet of snippetsByCursor) {
                if (snippet.startLine === bestSnippet.startLine && snippet.endLine === bestSnippet.endLine) {
                    bestSnippet.score += snippet.score;
                    break;
                }
            }

            return bestSnippet;
        }
    }

    /**
     * Returns the snippets from the object document that are most similar to the reference Document.
     * - The returned score
     *   - For CursorOnly, the returned score is the number clicks.
     *   - For CursorJaccard and JaccardCursor, The returned score is the sum of the number of clicks and the Jaccard similarity.
     *     The number of clicks is always a integer, and the Jaccard similarity is always a float less than 1.
     *     So we can assume that the code snippet will be sorted by the number of clicks first, and then by the Jaccard similarity.
     * - Algorithm
     *   - CursorOnly
     *     Pick up the top K clicked snippets.
     *   - CursorJaccard
     *     Generate all the snippet that was clicked more than 1 time.
     *     Calculate the Jaccard similarity between the snippet and the object document.
     *     Re-sort them by the number of clicks as the first key and Jaccard similarity as the second key.
     *     Pick up the top K snippets.
     *   - JaccardCursor
     *     Get the top K snippets by Jaccard similarity.
     *     Calculate the number of clicks for each snippet, and re-sort them.
     * @param objectDoc
     * @param snippetSelectionK
     * @returns
     */
    private async findTopKMatches(
        objectDoc: DocumentInfo,
        snippetSelectionK = 1
    ): Promise<SnippetWithProviderInfo[] | undefined> {
        if (objectDoc.source.length === 0 || snippetSelectionK < 1) {
            return undefined;
        }

        const lines = objectDoc.source.split('\n');
        let snippetsByCursor = this.retrieveCursorSnippets(objectDoc);
        if (snippetsByCursor.length === 0) {
            return undefined;
        }

        if (this.strategy === CursorSnippetsPickingStrategy.CursorOnly) {
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            let nonOverlappingSnippets: ScoredSnippetMarker[] = this.gatherNonOverlappingSnippets(
                snippetsByCursor,
                snippetSelectionK
            );
            return this.markerToSnippet(nonOverlappingSnippets, lines);
        } else if (this.strategy === CursorSnippetsPickingStrategy.CursorJaccard) {
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            const snippetCandidates = new Map<number, number>(snippetsByCursor.map(s => [s.startLine, s.endLine]));
            const allSnippetsSortedByJaccard = this.jaccardMatcher.retrieveAllSnippets(
                objectDoc,
                SortOptions.Descending,
                snippetCandidates
            );
            const jaccardMap = allSnippetsSortedByJaccard.reduce(
                (m, s) => m.set([s.startLine, s.endLine].join(','), s.score),
                new Map<string, number>()
            );

            // Sort the code snippet by 2 keys, the number of clicks is the primary key, and the second one is the jaccard similarity.
            // And I don't want to change the type of score. So I add them together and sort them.
            // We ignore the corner case where a jaccard similarity of 1 will behave like an additional click for the ordering
            snippetsByCursor.forEach(v => (v.score += jaccardMap.get([v.startLine, v.endLine].join(',')) ?? 0));
            snippetsByCursor = this.sortScoredSnippets(snippetsByCursor, SortOptions.Descending);
            let nonOverlappingSnippets: ScoredSnippetMarker[] = this.gatherNonOverlappingSnippets(
                snippetsByCursor,
                snippetSelectionK
            );
            return this.markerToSnippet(nonOverlappingSnippets, lines);
        } else if (this.strategy === CursorSnippetsPickingStrategy.JaccardCursor) {
            const topKByJaccard = await this.jaccardMatcher.findTopKMatches(objectDoc, snippetSelectionK);
            if (topKByJaccard === undefined) {
                return undefined;
            }

            const cursorMap = snippetsByCursor.reduce(
                (m, s) => m.set([s.startLine, s.endLine].join(','), s.score),
                new Map<string, number>()
            );
            topKByJaccard.forEach(v => (v.score += cursorMap.get([v.startLine, v.endLine].join(',')) ?? 0));
            const resortedTopKByJaccard = this.sortScoredSnippets(topKByJaccard, SortOptions.Descending);
            return this.markerToSnippet(resortedTopKByJaccard, lines);
        }
    }

    private gatherNonOverlappingSnippets(snippetsByCursor: ScoredSnippetMarker[], snippetSelectionK: number) {
        let nonOverlappingSnippets: ScoredSnippetMarker[] = [snippetsByCursor[0]];

        // step forward into snippets until finding a snippet that has no overlap with any snippet in nonOverlappingSnippets
        for (
            let currentIndex = 1;
            currentIndex < snippetsByCursor.length && nonOverlappingSnippets.length < snippetSelectionK;
            currentIndex++
        ) {
            if (
                nonOverlappingSnippets.findIndex(
                    snippet =>
                        snippetsByCursor[currentIndex].startLine < snippet.endLine &&
                        snippetsByCursor[currentIndex].endLine > snippet.startLine
                ) == -1
            ) {
                nonOverlappingSnippets.push(snippetsByCursor[currentIndex]);
            }
        }
        return nonOverlappingSnippets;
    }

    /**
     * Retrieve the snippets containing at least 1 cursor click. If there are less lines than the fixed size, we will get 1 snippet if it contains at least 1 click.
     */
    private retrieveCursorSnippets(objectDoc: DocumentInfo): ScoredSnippetMarker[] {
        const snippets: ScoredSnippetMarker[] = [];

        if (objectDoc.source.length === 0) {
            return snippets;
        }

        const cursors = this.lineCursorHistory.get(objectDoc.uri);
        if (cursors === undefined) {
            return snippets;
        }

        const lines = objectDoc.source.split('\n');

        enum pointType {
            leftBoundary,
            rightBoundary,
        }

        let sparsePoints: [number, pointType, number][] = [];
        for (const [line, num] of cursors.entries()) {
            if (line >= lines.length) {
                continue;
            }

            sparsePoints.push([Math.max(0, line - this.windowLength + 1), pointType.leftBoundary, num]);
            sparsePoints.push([line + 1, pointType.rightBoundary, num]);
        }

        sparsePoints.push([lines.length, pointType.leftBoundary, 0]);
        sparsePoints = sparsePoints.sort((a, b) => a[0] - b[0]);

        let numCursors = 0;
        let previousLine = 0;
        for (const [line, type, num] of sparsePoints) {
            if (numCursors > 0) {
                for (
                    let index = previousLine;
                    index < line && (index == 0 || index + this.windowLength <= lines.length);
                    index++
                ) {
                    snippets.push({
                        score: numCursors,
                        startLine: index,
                        endLine: Math.min(lines.length, index + this.windowLength),
                    });
                }
            }

            if (type === pointType.leftBoundary) {
                numCursors += num;
            } else {
                numCursors -= num;
            }

            previousLine = line;
        }

        return snippets;
    }
}
