// includes code which was modified from https://github.com/openai/gpt-2
//
// Important invariants about tokenization:
// - Tokenization works by a first pass that splits the text into chunks using
//   the regular expression `pat`.
// - Each chunk is then split into tokens.
// - All chunks' tokens are then concatenated to form the complete tokenization.
// - Newlines appear in only three tokens: "\n", "\n\n", "\n"+nonbreaking space
import * as fs from 'fs';
import * as path from 'path';
import {TextDecoder, TextEncoder} from 'util';

/////
// Helper functions
/////

const range = (x: number, y: number) => {
    const res = Array.from(Array(y).keys()).slice(x);
    return res;
};

const ord = (x: string) => {
    return x.charCodeAt(0);
};

const chr = (x: number) => {
    return String.fromCharCode(x);
};

const textDecoder = new TextDecoder('utf-8');
const decodeStr = (arr: number[]) => {
    return textDecoder.decode(new Uint8Array(arr));
};

const dictZip = (x: string[], y: number[]) => {
    const result = new Map<string, number>();
    x.forEach((_, i) => {
        result.set(x[i], y[i]);
    });
    return result;
};

function bytes_to_unicode(map: Map<number, string>) {
    const bs = range(ord('!'), ord('~') + 1).concat(range(ord(''), ord('') + 1), range(ord(''), ord('') + 1));

    let cs = bs.slice();
    let n = 0;
    for (let b = 0; b < 2 ** 8; b++) {
        if (!bs.includes(b)) {
            bs.push(b);
            cs.push(2 ** 8 + n);
            n = n + 1;
        }
    }

    const cs_ = cs.map(x => chr(x));
    for (let i = 0; i < bs.length; i++) {
        map.set(bs[i], cs_[i]);
    }
}

function get_char_pairs(word: string[]): Set<[string, string]> {
    const pairs = new Set<[string, string]>();
    let prev_char = word[0];
    for (let i = 1; i < word.length; i++) {
        const char = word[i];
        pairs.add([prev_char, char]);
        prev_char = char;
    }
    return pairs;
}

const pat = /'s|'t|'re|'ve|'m|'ll|'d| ?\p{L}+| ?\p{N}+| ?[^\s\p{L}\p{N}]+|\s+(?!\S)|\s+/gu;

export interface Tokenizer {
    /**
     * Returns the tokenization of the input string as a list of integers
     * representing tokens.
     */
    tokenize(text: string): Array<number>;

    /**
     * Returns the string representation of the tokens in `tokens`, given in integer
     * representation.
     *
     * This is the functional inverse of `tokenize`.
     */
    detokenize(tokens: number[]): string;

    /**
     * Returns the tokenization of the input string as a list of strings.
     *
     * The concatenation of the output of this function is equal to the input.
     */
    tokenizeStrings(text: string): string[];

    /**
     * Return the length of `text` in number of tokens.
     *
     * @param {str} text - The input text
     * @returns {number}
     */
    tokenLength(text: string): number;

    /**
     * Return a suffix of `text` which is `n` tokens long.
     * If `text` is at most `n` tokens, return `text`.
     *
     * Note: This implementation does not attempt to return
     * the longest possible suffix, only *some* suffix of at
     * most `n` tokens.
     *
     * @param {string} text - The text from which to take
     * @param {number} n - How many tokens to take
     * @returns A suffix of `text`, as a `string`.
     */
    takeLastTokens(text: string, n: number): string;

    /**
     * Return a prefix of `text` which is `n` tokens long.
     * If `text` is at most `n` tokens, return `text`.
     *
     * Note: This implementation does not attempt to return
     * the longest possible prefix, only *some* prefix of at
     * most `n` tokens.
     *
     * @param {string} text - The text from which to take
     * @param {number} n - How many tokens to take
     * @returns A prefix of `text`, as a `{ text: string, tokens: number[] }`.
     */
    takeFirstTokens(text: string, n: number): {text: string; tokens: number[]};

    /**
     * Return the longest suffix of `text` of complete lines and is at most
     * `n` tokens long.
     * @param {string} text - The text from which to take
     * @param {number} n - How many tokens to take
     */
    takeLastLinesTokens(text: string, n: number): string;
}

export enum TokenizerName {
    cushman001 = 'cushman001',
    cushman002 = 'cushman002',
    mock = 'mock',
}

const tokenizers = new Map<TokenizerName, Tokenizer>();

export function getTokenizer(name: TokenizerName = TokenizerName.cushman002): Tokenizer {
    let tokenizer = tokenizers.get(name);
    if (tokenizer !== undefined) {
        return tokenizer;
    }

    if (name === TokenizerName.mock) {
        tokenizer = new MockTokenizer();
    } else {
        tokenizer = new BPETokenizer(name);
    }
    tokenizers.set(name, tokenizer);
    return tokenizer;
}

class BPETokenizer implements Tokenizer {
    private decoder = new Map<number, string>();
    private encoder: Map<string, number>;
    private bpe_ranks: Map<string, number>;
    private byte_encoder = new Map<number, string>();
    private byte_decoder = new Map<string, number>();
    private cache = new Map();

    private textEncoder = new TextEncoder();

    constructor(name: Exclude<TokenizerName, TokenizerName.mock> = TokenizerName.cushman002) {
        let VOCAB = '';
        let ENCODER = '';
        if (name === TokenizerName.cushman001) {
            VOCAB = 'vocab_cushman001.bpe';
            ENCODER = 'tokenizer_cushman001.json';
        } else if (name === TokenizerName.cushman002) {
            VOCAB = 'vocab_cushman002.bpe';
            ENCODER = 'tokenizer_cushman002.json';
        } else {
            throw new Error(`Unknown tokenizer name: ${name}`);
        }
        const encoder_path = fs.readFileSync(path.resolve(__dirname, 'resources', name, ENCODER));
        const encoder_json = JSON.parse(encoder_path.toString());
        this.encoder = new Map(Object.entries(encoder_json));
        for (let [key, value] of this.encoder) {
            this.decoder.set(value, key);
        }

        const bpe_file = fs.readFileSync(path.resolve(__dirname, 'resources', name, VOCAB), 'utf-8');
        const bpe_merges = bpe_file
            .split('\n')
            .slice(1)
            .filter(l => l.trim().length > 0);
        this.bpe_ranks = dictZip(bpe_merges, range(0, bpe_merges.length));

        bytes_to_unicode(this.byte_encoder);
        this.byte_encoder.forEach((value, key, _) => {
            this.byte_decoder.set(value, key);
        });
    }

    private encodeStr = (str: string): number[] => {
        return Array.from(this.textEncoder.encode(str));
    };

    private byteEncodeStr(s: string) {
        return this.encodeStr(s).map(x => this.byte_encoder.get(x)!);
    }

    private bpe(chunk: string): number[] {
        if (this.cache.has(chunk)) {
            return this.cache.get(chunk);
        }
        let bytes = this.byteEncodeStr(chunk);
        let pairs = get_char_pairs(bytes);
        if (!pairs) {
            return bytes.map(x => this.encoder.get(x)!);
        }

        while (true) {
            const minPairs = new Map<Number, [string, string]>();
            pairs.forEach(pair => {
                const joined_pair = pair.join(' ');
                const rank = this.bpe_ranks.get(joined_pair);
                minPairs.set(rank === undefined || isNaN(rank) ? 10e10 : rank, pair);
            });

            const minPairsKeys = Array.from(minPairs.keys()).map(x => Number(x));

            const bigram = minPairs.get(Math.min(...minPairsKeys));

            if (!bigram || !this.bpe_ranks.has(bigram.join(' '))) {
                break;
            }

            const first = bigram[0];
            const second = bigram[1];
            let new_bytes = [];
            let i = 0;

            while (i < bytes.length) {
                const j = bytes.indexOf(first, i);
                if (j === -1) {
                    Array.prototype.push.apply(new_bytes, bytes.slice(i));
                    break;
                }
                Array.prototype.push.apply(new_bytes, bytes.slice(i, j));
                i = j;

                if (bytes[i] === first && i < bytes.length - 1 && bytes[i + 1] === second) {
                    new_bytes.push(first + second);
                    i = i + 2;
                } else {
                    new_bytes.push(bytes[i]);
                    i = i + 1;
                }
            }

            bytes = new_bytes;
            if (bytes.length === 1) {
                break;
            } else {
                pairs = get_char_pairs(bytes);
            }
        }

        const tokens = bytes.map(x => this.encoder.get(x)!);
        this.cache.set(chunk, tokens);
        return tokens;
    }

    tokenize(text: string): number[] {
        let tokens: number[] = [];
        const matches = Array.from(text.matchAll(pat)).map(x => x[0]);
        for (let chunk of matches) {
            const chunk_tokens = this.bpe(chunk);
            Array.prototype.push.apply(tokens, chunk_tokens);
        }
        return tokens;
    }

    tokenLength(text: string): number {
        return this.tokenize(text).length;
    }

    takeLastTokens(text: string, n: number): string {
        if (n <= 0) return '';

        // Find long enough suffix of text that has >= n + 2 tokens
        // We add the 2 extra tokens to avoid the edge case where
        // we cut at exactly n tokens and may get an odd tokenization.
        const CHARS_PER_TOKENS_START = 4;
        const CHARS_PER_TOKENS_ADD = 1;
        let chars = Math.min(text.length, n * CHARS_PER_TOKENS_START); //First guess
        let suffix = text.slice(-chars);
        let suffixT = this.tokenize(suffix);
        while (suffixT.length < n + 2 && chars < text.length) {
            chars = Math.min(text.length, chars + n * CHARS_PER_TOKENS_ADD);
            suffix = text.slice(-chars);
            suffixT = this.tokenize(suffix);
        }
        if (suffixT.length < n) {
            // text must be <= n tokens long
            return text;
        }
        // Return last n tokens
        suffixT = suffixT.slice(-n);
        return this.detokenize(suffixT);
    }

    takeFirstTokens(text: string, n: number): {text: string; tokens: number[]} {
        if (n <= 0) return {text: '', tokens: []};

        // Find long enough suffix of text that has >= n + 2 tokens
        // We add the 2 extra tokens to avoid the edge case where
        // we cut at exactly n tokens and may get an odd tokenization.
        const CHARS_PER_TOKENS_START = 4;
        const CHARS_PER_TOKENS_ADD = 1;
        let chars = Math.min(text.length, n * CHARS_PER_TOKENS_START); //First guess
        let prefix = text.slice(0, chars);
        let prefix_t = this.tokenize(prefix);
        while (prefix_t.length < n + 2 && chars < text.length) {
            chars = Math.min(text.length, chars + n * CHARS_PER_TOKENS_ADD);
            prefix = text.slice(0, chars);
            prefix_t = this.tokenize(prefix);
        }
        if (prefix_t.length < n) {
            // text must be <= n tokens long
            return {
                text: text,
                tokens: prefix_t,
            };
        }
        // Return first n tokens
        prefix_t = prefix_t.slice(0, n);
        return {
            text: this.detokenize(prefix_t),
            tokens: prefix_t,
        };
    }

    takeLastLinesTokens(text: string, n: number): string {
        const suffix = this.takeLastTokens(text, n);
        if (suffix.length === text.length || text[text.length - suffix.length - 1] === '\n') {
            // Edge case: We already took whole lines
            return suffix;
        }
        let newline = suffix.indexOf('\n');
        return suffix.substring(newline + 1);
    }

    detokenize(tokens: number[]): string {
        let text = tokens.map(x => this.decoder.get(x)).join('');
        text = decodeStr(text.split('').map(x => this.byte_decoder.get(x)!));
        return text;
    }

    tokenizeStrings(text: string): string[] {
        const tokens = this.tokenize(text);
        return tokens.map(token =>
            decodeStr(
                this.decoder
                    .get(token)!
                    .split('')
                    .map(char => this.byte_decoder.get(char)!)
            )
        );
    }
}

class MockTokenizer implements Tokenizer {
    private hash = (str: string) => {
        let hash = 0;
        for (let i = 0; i < str.length; i++) {
            const char = str.charCodeAt(i);
            hash = (hash << 5) - hash + char;
            hash &= hash & 0xffff;
        }
        return hash;
    };

    tokenize(text: string): number[] {
        return this.tokenizeStrings(text).map(this.hash);
    }
    detokenize(tokens: number[]): string {
        return tokens.map(token => token.toString()).join(' ');
    }
    tokenizeStrings(text: string): string[] {
        return text.split(/\b/);
    }
    tokenLength(text: string): number {
        return this.tokenizeStrings(text).length;
    }
    takeLastTokens(text: string, n: number): string {
        return this.tokenizeStrings(text).slice(-n).join('');
    }
    takeFirstTokens(text: string, n: number): {text: string; tokens: number[]} {
        const tokens = this.tokenizeStrings(text).slice(0, n);
        return {text: tokens.join(''), tokens: tokens.map(this.hash)};
    }
    takeLastLinesTokens(text: string, n: number): string {
        const suffix = this.takeLastTokens(text, n);
        if (suffix.length === text.length || text[text.length - suffix.length - 1] === '\n') {
            // Edge case: We already took whole lines
            return suffix;
        }
        let newline = suffix.indexOf('\n');
        return suffix.substring(newline + 1);
    }
}
